import React from 'react';
import { X, FileText, Calendar, Package, Building2, ChevronDown, ChevronRight, User, Clock, Lock, Unlock, Settings, CreditCard as Edit, Trash2, Eye, Download } from 'lucide-react';
import { InspectionContent } from '../../types/inspection';
import { useLanguage } from '../../contexts/LanguageContext';
import { getLoggedLevel, getLoggedName } from '../../services/auth';
import { updateSubContent, deleteSubContent, getTextVisionByMasterGUID, downloadExcelByContent } from '../../services/inspection';
import LoadingSpinner from '../common/LoadingSpinner';

interface InspectionListModalProps {
  isOpen: boolean;
  onClose: () => void;
  inspections: InspectionContent[];
  title?: string;
  onDataUpdate?: () => void;
  isToday?: boolean;
  onDateRangeChange?: (startDate: string, endDate: string) => void;
  initialDateRange?: [string, string];
}

export const InspectionListModal: React.FC<InspectionListModalProps> = ({
  isOpen,
  onClose,
  inspections,
  title = "",
  onDataUpdate,
  isToday = false,
  onDateRangeChange,
  initialDateRange,
}) => {
  const [expandedRows, setExpandedRows] = React.useState<Set<string>>(new Set());
  const [lockingItems, setLockingItems] = React.useState<Set<string>>(new Set());
  const [error, setError] = React.useState<string | null>(null);
  const [batchLockLoading, setBatchLockLoading] = React.useState(false);
  const [showDateSettings, setShowDateSettings] = React.useState(false);
  const [startDate, setStartDate] = React.useState('');
  const [startTime, setStartTime] = React.useState('00:00:00');
  const [endDate, setEndDate] = React.useState('');
  const [endTime, setEndTime] = React.useState('23:59:59');
  const [editingSubContent, setEditingSubContent] = React.useState<any>(null);
  const [editForm, setEditForm] = React.useState({
    lot: '',
    endQty: '',
    val: '',
  });
  const [updateLoading, setUpdateLoading] = React.useState<string | null>(null);
  const [deleteConfirmModal, setDeleteConfirmModal] = React.useState<{
    isOpen: boolean;
    subContent: any;
    inspectionItem: any;
  }>({ isOpen: false, subContent: null, inspectionItem: null });
  const [deleteLoading, setDeleteLoading] = React.useState<string | null>(null);
  const [imageViewModal, setImageViewModal] = React.useState<{ isOpen: boolean; imageUrl: string; title: string }>({
    isOpen: false,
    imageUrl: '',
    title: ''
  });
  const [imageLoading, setImageLoading] = React.useState<string | null>(null);
  const [downloadLoading, setDownloadLoading] = React.useState(false);
  const { t } = useLanguage();

  // 
  React.useEffect(() => {
    if (isToday) {
      const now = new Date();
      const today = now.toISOString().split('T')[0];
      setStartDate(today);
      setEndDate(today);
      setStartTime('00:00:00');
      setEndTime('23:59:59');
    } else if (initialDateRange) {
      // 
      const [start, end] = initialDateRange;
      const startParts = start.split(' ');
      const endParts = end.split(' ');

      setStartDate(startParts[0]);
      setStartTime(startParts[1] || '00:00:00');
      setEndDate(endParts[0]);
      setEndTime(endParts[1] || '23:59:59');
    }
  }, [isToday, initialDateRange]);

  // 
  const userLevel = getLoggedLevel();
  const canLockUnlock = userLevel === '20';

  // 
  const formatArrivalTime = (deliveryTime: string): string => {
    if (!deliveryTime) return '';
    
    try {
      // 
      const deliveryDate = new Date(deliveryTime);
      const today = new Date();
      
      // 
      deliveryDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);
      
      // 
      const timeDiff = deliveryDate.getTime() - today.getTime();
      const daysDiff = Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
      
      // 
      const formattedDate = deliveryDate.toLocaleDateString('zh-TW', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      }).replace(/\//g, '/');
      
      // 
      if (daysDiff === 0) {
        return `${formattedDate} (${t('delivery.today')})`;
      } else if (daysDiff < 0) {
        return `${formattedDate} (${t('delivery.overdue')} ${Math.abs(daysDiff)} ${t('delivery.days')})`;
      } else {
        return `${formattedDate} (${t('delivery.remaining')} ${daysDiff} ${t('delivery.days')})`;
      }
    } catch (error) {
      console.error('Error formatting arrival time:', error);
      return deliveryTime;
    }
  };

  // 
  const getArrivalTimeColor = (deliveryTime: string): string => {
    if (!deliveryTime) return 'text-gray-500';
    
    try {
      const deliveryDate = new Date(deliveryTime);
      const today = new Date();
      
      deliveryDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);
      
      const timeDiff = deliveryDate.getTime() - today.getTime();
      const daysDiff = Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
      
      if (daysDiff === 0) {
        return 'text-blue-600 font-medium'; // 
      } else if (daysDiff < 0) {
        return 'text-red-600 font-medium'; // 
      } else {
        return 'text-green-600 font-medium'; // 
      }
    } catch (error) {
      return 'text-gray-500';
    }
  };
  const toggleRow = (guid: string) => {
    setExpandedRows(prev => {
      const newSet = new Set(prev);
      if (newSet.has(guid)) {
        newSet.delete(guid);
      } else {
        newSet.add(guid);
      }
      return newSet;
    });
  };

  // /
  const handleLockUnlock = async (subContent: any, newState: '' | '') => {
    if (!canLockUnlock) {
      setError('');
      return;
    }

    setLockingItems(prev => new Set([...prev, subContent.GUID]));
    setError(null);

    try {
      console.log('=== Lock/Unlock Sub Content Debug ===');
      console.log('Sub Content GUID:', subContent.GUID);
      console.log('Current State:', subContent.STATE);
      console.log('New State:', newState);

      //  STATE 
      const updateData = {
        ...subContent,
        STATE: newState,
        //  OP_TIME  service 
      };

      console.log('Update Data:', JSON.stringify(updateData, null, 2));

      const response = await updateSubContent(updateData);

      console.log('Lock/Unlock Response:', JSON.stringify(response, null, 2));

      if (response.Code === 200) {
        console.log(` ${newState === '' ? '' : ''}`);
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
      } else {
        setError(response.Result || `${newState === '' ? '' : ''}`);
      }
    } catch (err) {
      console.error('Lock/Unlock error:', err);
      setError(err instanceof Error ? err.message : `${newState === '' ? '' : ''}`);
    } finally {
      setLockingItems(prev => {
        const newSet = new Set(prev);
        newSet.delete(subContent.GUID);
        return newSet;
      });
    }
  };

  // /
  const handleBatchLockUnlock = async () => {
    if (!canLockUnlock) {
      setError('');
      return;
    }

    // 
    const allSubContents: any[] = [];
    inspections.forEach(inspection => {
      if (inspection.Sub_content && inspection.Sub_content.length > 0) {
        allSubContents.push(...inspection.Sub_content);
      }
    });

    if (allSubContents.length === 0) {
      setError('');
      return;
    }

    // 
    const newState = '';
    const actionName = '';

    setBatchLockLoading(true);
    setError(null);

    try {
      console.log(`=== ${actionName} Debug ===`);
      console.log(':', allSubContents.length);
      console.log(':', newState);

      let successCount = 0;
      let failCount = 0;
      const errors: string[] = [];

      // 
      for (const subContent of allSubContents) {
        try {
          const updateData = {
            ...subContent,
            STATE: newState,
          };

          const response = await updateSubContent(updateData);

          if (response.Code === 200) {
            successCount++;
            console.log(` ${subContent.GUID} ${actionName}`);
          } else {
            failCount++;
            errors.push(`${subContent.GUID}: ${response.Result}`);
            console.log(` ${subContent.GUID} ${actionName}:`, response.Result);
          }
        } catch (err) {
          failCount++;
          const errorMsg = err instanceof Error ? err.message : '';
          errors.push(`${subContent.GUID}: ${errorMsg}`);
          console.error(` ${subContent.GUID} ${actionName}:`, err);
        }
      }

      console.log(`=== ${actionName} ===`);
      console.log(':', successCount);
      console.log(':', failCount);

      if (successCount > 0) {
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
        
        if (failCount === 0) {
          // 
          console.log(` ${actionName}`);
        } else {
          // 
          setError(`${successCount} ${failCount} ${errors.join('; ')}`);
        }
      } else {
        // 
        setError(`${actionName}${errors.join('; ')}`);
      }

    } catch (err) {
      console.error(`${actionName}:`, err);
      setError(err instanceof Error ? err.message : `${actionName}`);
    } finally {
      setBatchLockLoading(false);
    }
  };

  // 
  const handleEditSubContent = (subContent: any) => {
    setEditingSubContent(subContent);
    
    //  YYYY-MM-DD 
    let formattedDate = '';
    if (subContent.VAL) {
      try {
        const date = new Date(subContent.VAL);
        formattedDate = date.toISOString().split('T')[0];
      } catch (error) {
        console.warn('Invalid date format:', subContent.VAL);
      }
    }

    setEditForm({
      lot: subContent.LOT || '',
      endQty: subContent.END_QTY || '',
      val: formattedDate,
    });
  };

  // 
  const handleEditCancel = () => {
    setEditingSubContent(null);
    setEditForm({
      lot: '',
      endQty: '',
      val: '',
    });
  };

  // 
  const handleEditSave = async (subContent: any) => {
    setUpdateLoading(subContent.GUID);
    setError(null);

    try {
      const loggedName = getLoggedName();
      if (!loggedName) {
        setError('');
        return;
      }

      //  API  (yyyy/MM/dd HH:mm:ss)
      const formattedVal = editForm.val ? `${editForm.val} 00:00:00` : '';

      // 
      const updateData = {
        ...subContent,
        LOT: editForm.lot,
        END_QTY: editForm.endQty,
        VAL: formattedVal,
        OP: loggedName,
      };

      const response = await updateSubContent(updateData);

      if (response.Code === 200) {
        console.log(' ');
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
        
        // 
        setEditingSubContent(null);
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setUpdateLoading(null);
    }
  };

  // 
  const handleDeleteSubContent = (subContent: any, inspectionItem: any) => {
    setDeleteConfirmModal({
      isOpen: true,
      subContent,
      inspectionItem
    });
  };

  // 
  const handleDeleteConfirm = async () => {
    const { subContent } = deleteConfirmModal;
    if (!subContent) return;

    setDeleteLoading(subContent.GUID);
    setError(null);

    try {
      const response = await deleteSubContent(
        subContent.GUID,
        subContent.Master_GUID
      );

      if (response.Code === 200) {
        console.log(' ');
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
        
        // 
        setDeleteConfirmModal({ isOpen: false, subContent: null, inspectionItem: null });
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setDeleteLoading(null);
    }
  };

  // 
  const handleViewImage = async (itemGuid: string, itemName: string) => {
    setImageLoading(itemGuid);
    
    try {
      console.log('===  ===');
      console.log('Item GUID:', itemGuid);
      
      const response = await getTextVisionByMasterGUID(itemGuid);
      
      console.log('API Response:', response);
      
      if (response.Code === 200 && response.Data) {
        const imageData = response.Data;
        console.log('Image Data:', imageData);
        
        if (imageData.base64) {
          const imageUrl = imageData.base64.startsWith('data:') 
            ? imageData.base64 
            : `data:image/jpeg;base64,${imageData.base64}`;
          
          setImageViewModal({
            isOpen: true,
            imageUrl: imageUrl,
            title: `${itemName} - `
          });
        } else {
          alert('');
        }
      } else {
        alert('');
      }
    } catch (err) {
      console.error(':', err);
      alert('');
    } finally {
      setImageLoading(null);
    }
  };

  // 
  const handleDateSubmit = () => {
    if (onDateRangeChange && startDate && endDate) {
      const formattedStartDate = `${startDate} ${startTime}`;
      const formattedEndDate = `${endDate} ${endTime}`;
      onDateRangeChange(formattedStartDate, formattedEndDate);
      setShowDateSettings(false);
    }
  };

  // 
  const handleDownloadExcel = async () => {
    if (inspections.length === 0) {
      alert('');
      return;
    }

    setDownloadLoading(true);
    setError(null);

    try {
      console.log('===  ===');
      console.log(':', inspections.length);

      const blob = await downloadExcelByContent(inspections);

      console.log(' Blob:', blob);

      // 
      const fileName = `_${startDate || 'all'}_${endDate || 'all'}.xlsx`;

      // 
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = fileName;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      window.URL.revokeObjectURL(url);

      console.log(' ');
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setDownloadLoading(false);
    }
  };

  // 
  const ImageViewModal: React.FC<{ isOpen: boolean; onClose: () => void; imageUrl: string; title: string }> = ({ isOpen, onClose, imageUrl, title }) => {
    if (!isOpen) return null;

    return (
      <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center p-4 z-[60]">
        <div className="relative max-w-[90vw] max-h-[90vh]">
          <button
            onClick={onClose}
            className="absolute -top-10 right-0 text-white hover:text-gray-300 transition-colors"
          >
            <X className="w-8 h-8" />
          </button>
          <img
            src={imageUrl}
            alt={title}
            className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
          />
          <div className="absolute bottom-0 left-0 right-0 bg-black bg-opacity-50 text-white p-2 rounded-b-lg">
            <p className="text-center text-sm">{title}</p>
          </div>
        </div>
      </div>
    );
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-6xl w-full max-h-[90vh] overflow-hidden">
        <div className="flex items-center justify-between p-6 border-b bg-gray-50">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <FileText className="w-5 h-5 mr-2" />
              {title}
              <button
                onClick={() => setShowDateSettings(!showDateSettings)}
                className="ml-2 text-gray-400 hover:text-gray-600 transition-colors"
                title=""
              >
                <Settings className="w-5 h-5" />
              </button>
            </h3>
            <p className="text-sm text-gray-600 mt-1">
               {inspections.length} 
            </p>
          </div>
          <div className="flex items-center gap-3">
            {/*  */}
            <button
              onClick={handleDownloadExcel}
              disabled={downloadLoading || inspections.length === 0}
              className="inline-flex items-center px-3 py-2 text-sm font-medium rounded-md bg-green-600 text-white hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              title=" Excel"
            >
              {downloadLoading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Download className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>

            {/* / */}
            <button
              onClick={handleBatchLockUnlock}
              disabled={!canLockUnlock || batchLockLoading || inspections.length === 0}
              className={`inline-flex items-center px-3 py-2 text-sm font-medium rounded-md transition-colors ${
                canLockUnlock
                  ? 'bg-blue-600 text-white hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed'
                  : 'bg-gray-300 text-gray-500 cursor-not-allowed'
              }`}
              title={canLockUnlock ? '/' : ''}
            >
              {batchLockLoading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Lock className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <X className="w-6 h-6" />
            </button>
          </div>
        </div>

        <div className="p-6 overflow-y-auto max-h-[calc(90vh-120px)]">
          {/*  */}
          {showDateSettings && (
            <div className="mb-6 p-4 bg-gray-50 rounded-lg border">
              <h4 className="text-sm font-medium text-gray-700 mb-3"></h4>
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-4 items-end">
                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    
                  </label>
                  <div className="flex gap-3">
                    <input
                      type="date"
                      value={startDate}
                      onChange={(e) => setStartDate(e.target.value)}
                      className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    />
                    <input
                      type="time"
                      value={startTime}
                      onChange={(e) => setStartTime(e.target.value)}
                      className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      step="1"
                    />
                  </div>
                </div>

                {/*  */}
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    
                  </label>
                  <div className="flex gap-3">
                    <input
                      type="date"
                      value={endDate}
                      onChange={(e) => setEndDate(e.target.value)}
                      className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    />
                    <input
                      type="time"
                      value={endTime}
                      onChange={(e) => setEndTime(e.target.value)}
                      className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                      step="1"
                    />
                  </div>
                </div>
              </div>
              <div className="flex justify-end gap-2 mt-4">
                <button
                  onClick={handleDateSubmit}
                  disabled={!startDate || !endDate || !startTime || !endTime}
                  className="px-3 py-2 text-sm bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                >
                  
                </button>
              </div>
            </div>
          )}

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          {inspections.length === 0 ? (
            <div className="text-center py-8">
              <Package className="w-12 h-12 text-gray-400 mx-auto mb-4" />
              <p className="text-gray-600"></p>
            </div>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.inspection_number')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.purchase_number')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.drug_code')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.item_number')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.drug_name')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.manufacturer')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.arrival_time')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      {t('table.received_expected_qty')}
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {inspections.map((inspection) => {
                    const receivedQty = inspection.Sub_content?.reduce((sum, sub) => {
                      return sum + (parseInt(sub.END_QTY) || 0);
                    }, 0) || 0;
                    const isExpanded = expandedRows.has(inspection.GUID);
                    const hasSubContent = inspection.Sub_content && inspection.Sub_content.length > 0;

                    return (
                      <>
                        <tr 
                          key={inspection.GUID} 
                          className={`hover:bg-gray-50 ${hasSubContent ? 'cursor-pointer' : ''}`}
                          onClick={() => hasSubContent && toggleRow(inspection.GUID)}
                        >
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className="text-sm font-medium text-gray-900">
                            {inspection.IC_SN}
                          </span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className="text-sm text-gray-900">{inspection.PON}</span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className="text-sm text-gray-900">{inspection.CODE}</span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className="text-sm text-gray-900">{inspection.SKDIACODE}</span>
                        </td>
                        <td className="px-6 py-4">
                          <div className="text-sm text-gray-900">
                            <div className="font-medium">
                              {inspection.CHT_NAME || inspection.NAME}
                            </div>
                            {inspection.CHT_NAME && inspection.NAME && inspection.CHT_NAME !== inspection.NAME && (
                              <div className="text-gray-500 text-xs mt-1">
                                {inspection.NAME}
                              </div>
                            )}
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span className="text-sm text-gray-900">
                            {inspection.BRD || ''}
                          </span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className={`text-sm flex items-center ${getArrivalTimeColor(inspection.DELIVERY_TIME)}`}>
                            <Calendar className="w-4 h-4 mr-1 text-gray-400" />
                            {formatArrivalTime(inspection.DELIVERY_TIME)}
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="text-sm text-gray-900">
                            <span className="font-semibold text-green-600">
                              {receivedQty.toLocaleString()}
                            </span>
                            <span className="text-gray-400 mx-1">/</span>
                            <span className="font-semibold text-blue-600">
                              {parseInt(inspection.START_QTY).toLocaleString()}
                            </span>
                            <span className="text-gray-500 ml-1">
                              {inspection.PAKAGE}
                            </span>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-right">
                          {hasSubContent && (
                            isExpanded ? (
                              <ChevronDown className="w-5 h-5 text-gray-400" />
                            ) : (
                              <ChevronRight className="w-5 h-5 text-gray-400" />
                            )
                          )}
                        </td>
                        </tr>
                        {isExpanded && hasSubContent && (
                        inspection.Sub_content.map((subItem, subIndex) => (
                          <tr key={`${inspection.GUID}-sub-${subIndex}`} className="bg-gray-50">
                            <td colSpan={9} className="px-6 py-4">
                              <div className="bg-white rounded-md p-4 border border-gray-200 ml-8">
                                <div className="flex justify-between items-start mb-3">
                                  <h6 className="text-sm font-medium text-gray-900"> #{subIndex + 1}</h6>
                                  <div className="flex gap-2">
                                    {/*  */}
                                    {subItem.STATE !== '' && (
                                      <>
                                        {editingSubContent?.GUID === subItem.GUID ? (
                                          <>
                                            <button
                                              onClick={() => handleEditSave(subItem)}
                                              disabled={updateLoading === subItem.GUID}
                                              className="inline-flex items-center px-2 py-1 bg-blue-600 text-white text-xs rounded hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                            >
                                              {updateLoading === subItem.GUID ? (
                                                <LoadingSpinner size="small" className="mr-1" />
                                              ) : (
                                                <svg className="w-3 h-3 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                                                </svg>
                                              )}
                                              
                                            </button>
                                            <button
                                              onClick={handleEditCancel}
                                              disabled={updateLoading === subItem.GUID}
                                              className="inline-flex items-center px-2 py-1 bg-gray-600 text-white text-xs rounded hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                            >
                                              <X className="w-3 h-3 mr-1" />
                                              
                                            </button>
                                          </>
                                        ) : (
                                          <>
                                            <button
                                              onClick={() => handleEditSubContent(subItem)}
                                              className="inline-flex items-center px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded hover:bg-gray-200 transition-colors"
                                              title=""
                                            >
                                              <Edit className="w-3 h-3 mr-1" />
                                              
                                            </button>
                                            <button
                                              onClick={() => handleDeleteSubContent(subItem, inspection)}
                                              disabled={deleteLoading === subItem.GUID}
                                              className="inline-flex items-center px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                              title=""
                                            >
                                              {deleteLoading === subItem.GUID ? (
                                                <LoadingSpinner size="small" className="mr-1" />
                                              ) : (
                                                <Trash2 className="w-3 h-3 mr-1" />
                                              )}
                                              
                                            </button>
                                          </>
                                        )}
                                      </>
                                    )}
                                    
                                    {/*  */}
                                    <button
                                      onClick={() => handleViewImage(inspection.GUID, inspection.CHT_NAME || inspection.NAME)}
                                      disabled={imageLoading === inspection.GUID}
                                      className="inline-flex items-center px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded hover:bg-gray-200 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                      title=""
                                    >
                                      {imageLoading === inspection.GUID ? (
                                        <LoadingSpinner size="small" className="mr-1" />
                                      ) : (
                                        <Eye className="w-3 h-3 mr-1" />
                                      )}
                                      
                                    </button>
                                    
                                    {/* / */}
                                    {canLockUnlock && (
                                    <div className="flex gap-2">
                                      {subItem.STATE === '' ? (
                                        <button
                                          onClick={() => handleLockUnlock(subItem, '')}
                                          disabled={lockingItems.has(subItem.GUID)}
                                          className="inline-flex items-center px-2 py-1 bg-gray-600 text-white text-xs rounded hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                          title=""
                                        >
                                          {lockingItems.has(subItem.GUID) ? (
                                            <LoadingSpinner size="small" className="mr-1" />
                                          ) : (
                                            <Lock className="w-3 h-3 mr-1" />
                                          )}
                                          
                                        </button>
                                      ) : (
                                        <button
                                          onClick={() => handleLockUnlock(subItem, '')}
                                          disabled={lockingItems.has(subItem.GUID)}
                                          className="inline-flex items-center px-2 py-1 bg-gray-600 text-white text-xs rounded hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                          title=""
                                        >
                                          {lockingItems.has(subItem.GUID) ? (
                                            <LoadingSpinner size="small" className="mr-1" />
                                          ) : (
                                            <Unlock className="w-3 h-3 mr-1" />
                                          )}
                                          
                                        </button>
                                      )}
                                    </div>
                                    )}
                                  </div>
                                </div>
                                
                                {/*  */}
                                {editingSubContent?.GUID === subItem.GUID ? (
                                  // 
                                  <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                    <div>
                                      <label className="block text-xs font-medium text-gray-500 uppercase mb-1"></label>
                                      <input
                                        type="text"
                                        value={editForm.lot}
                                        onChange={(e) => setEditForm(prev => ({ ...prev, lot: e.target.value }))}
                                        className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                      />
                                    </div>
                                    <div>
                                      <label className="block text-xs font-medium text-gray-500 uppercase mb-1"></label>
                                      <input
                                        type="number"
                                        value={editForm.endQty}
                                        onChange={(e) => setEditForm(prev => ({ ...prev, endQty: e.target.value }))}
                                        className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                      />
                                    </div>
                                    <div>
                                      <label className="block text-xs font-medium text-gray-500 uppercase mb-1"></label>
                                      <input
                                        type="date"
                                        value={editForm.val}
                                        onChange={(e) => setEditForm(prev => ({ ...prev, val: e.target.value }))}
                                        className="w-full px-2 py-1 text-sm border border-gray-300 rounded focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                                      />
                                    </div>
                                  </div>
                                ) : (
                                  // 
                                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                                  <div>
                                    <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                    <div className="text-sm space-y-1">
                                      <div><span className="text-gray-500">:</span> <span className="font-medium text-green-600">{subItem.END_QTY} {subItem.PAKAGE}</span></div>
                                    </div>
                                  </div>
                                  
                                  <div>
                                    <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                    <div className="text-sm space-y-1">
                                      <div><span className="text-gray-500">:</span> {subItem.VAL ? new Date(subItem.VAL).toLocaleDateString('zh-TW', { year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//g, '/') : ''}</div>
                                      <div><span className="text-gray-500">:</span> {subItem.LOT}</div>
                                    </div>
                                  </div>
                                  
                                  <div>
                                    <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                    <div className="text-sm space-y-1">
                                      <div className="flex items-center">
                                        <User className="w-3 h-3 text-gray-400 mr-1" />
                                        {subItem.OP}
                                      </div>
                                      <div className="flex items-center">
                                        <Calendar className="w-3 h-3 text-gray-400 mr-1" />
                                        {subItem.OP_TIME}
                                      </div>
                                    </div>
                                  </div>
                                  
                                  <div>
                                    <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                    <div className="text-sm">
                                      <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                                        subItem.STATE === '' 
                                          ? 'bg-yellow-100 text-yellow-800' 
                                          : 'bg-green-100 text-green-800'
                                      }`}>
                                        {subItem.STATE}
                                      </span>
                                    </div>
                                  </div>
                                  </div>
                                )}
                                
                                {(subItem.BARCODE1 || subItem.BARCODE2 || subItem.NOTE) && (
                                  <div className="mt-3 pt-3 border-t border-gray-100">
                                    {(subItem.BARCODE1 || subItem.BARCODE2) && (
                                      <div className="mb-2">
                                        <span className="text-xs font-medium text-gray-500 uppercase"></span>
                                        <div className="text-sm text-gray-600 mt-1">
                                          {subItem.BARCODE1 && <div>1: {subItem.BARCODE1}</div>}
                                          {subItem.BARCODE2 && <div>2: {subItem.BARCODE2}</div>}
                                        </div>
                                      </div>
                                    )}
                                    {subItem.NOTE && (
                                      <div>
                                        <span className="text-xs font-medium text-gray-500 uppercase"></span>
                                        <p className="text-sm text-gray-600 mt-1">{subItem.NOTE}</p>
                                      </div>
                                    )}
                                  </div>
                                )}
                              </div>
                            </td>
                          </tr>
                        ))
                        )}
                      </>
                    );
                  })}
                </tbody>
              </table>
            </div>
          )}
        </div>

        <div className="flex justify-end p-6 border-t bg-gray-50">
          <button
            onClick={onClose}
            className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
          >
            
          </button>
        </div>
      </div>
      
      {/*  */}
      {deleteConfirmModal.isOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-[60]">
          <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
            <div className="flex items-center justify-between p-6 border-b">
              <h3 className="text-lg font-semibold text-gray-900"></h3>
              <button
                onClick={() => setDeleteConfirmModal({ isOpen: false, subContent: null, inspectionItem: null })}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <X className="w-6 h-6" />
              </button>
            </div>
            <div className="p-6">
              <p className="text-sm text-gray-600 mb-4">
                
              </p>
              <div className="bg-gray-50 rounded-lg p-4 space-y-2 mb-6">
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">:</span>
                  <span className="text-sm font-medium text-gray-900">{deleteConfirmModal.inspectionItem?.CHT_NAME || deleteConfirmModal.inspectionItem?.NAME}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">:</span>
                  <span className="text-sm font-medium text-gray-900">{deleteConfirmModal.subContent?.LOT}</span>
                </div>
                <div className="flex justify-between">
                  <span className="text-sm text-gray-500">:</span>
                  <span className="text-sm font-medium text-gray-900">{deleteConfirmModal.subContent?.END_QTY} {deleteConfirmModal.subContent?.PAKAGE}</span>
                </div>
              </div>
              <div className="flex gap-3">
                <button
                  onClick={() => setDeleteConfirmModal({ isOpen: false, subContent: null, inspectionItem: null })}
                  className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
                  disabled={deleteLoading}
                >
                  
                </button>
                <button
                  onClick={handleDeleteConfirm}
                  disabled={deleteLoading}
                  className="flex-1 px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
                >
                  {deleteLoading ? (
                    <>
                      <LoadingSpinner size="small" className="mr-2" />
                      ...
                    </>
                  ) : (
                    <>
                      <Trash2 className="w-4 h-4 mr-2" />
                      
                    </>
                  )}
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
      
      {/*  */}
      <ImageViewModal
        isOpen={imageViewModal.isOpen}
        onClose={() => setImageViewModal({ isOpen: false, imageUrl: '', title: '' })}
        imageUrl={imageViewModal.imageUrl}
        title={imageViewModal.title}
      />
    </div>
  );
};