import React, { useState, useEffect, useMemo, useRef } from 'react';
import { X, Plus, ChevronDown } from 'lucide-react';
import { getMedCloudData } from '../../services/inspection';
import { apiCall } from '../../utils/api';

interface MedData {
  NAME: string;
  CHT_NAME: string;
  CODE: string;
  SKDIACODE: string;
}

interface CreateBarcodeModalProps {
  isOpen: boolean;
  onClose: () => void;
  barcode: string;
  onSuccess: () => void;
}

export const CreateBarcodeModal: React.FC<CreateBarcodeModalProps> = ({
  isOpen,
  onClose,
  barcode,
  onSuccess,
}) => {
  const [medData, setMedData] = useState<MedData[]>([]);
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [searchName, setSearchName] = useState('');
  const [searchChtName, setSearchChtName] = useState('');
  const [searchCode, setSearchCode] = useState('');
  const [searchSkdiacode, setSearchSkdiacode] = useState('');

  const [selectedMed, setSelectedMed] = useState<MedData | null>(null);
  const [showNameDropdown, setShowNameDropdown] = useState(false);
  const [showChtNameDropdown, setShowChtNameDropdown] = useState(false);
  const [showCodeDropdown, setShowCodeDropdown] = useState(false);
  const [showSkdiacodeDropdown, setShowSkdiacodeDropdown] = useState(false);

  const nameDropdownRef = useRef<HTMLDivElement>(null);
  const chtNameDropdownRef = useRef<HTMLDivElement>(null);
  const codeDropdownRef = useRef<HTMLDivElement>(null);
  const skdiacodeDropdownRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (isOpen) {
      loadMedData();
    } else {
      resetForm();
    }
  }, [isOpen]);

  const resetForm = () => {
    setSearchName('');
    setSearchChtName('');
    setSearchCode('');
    setSearchSkdiacode('');
    setSelectedMed(null);
    setError(null);
    setShowNameDropdown(false);
    setShowChtNameDropdown(false);
    setShowCodeDropdown(false);
    setShowSkdiacodeDropdown(false);
  };

  // Click outside to close dropdowns
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (nameDropdownRef.current && !nameDropdownRef.current.contains(event.target as Node)) {
        setShowNameDropdown(false);
      }
      if (chtNameDropdownRef.current && !chtNameDropdownRef.current.contains(event.target as Node)) {
        setShowChtNameDropdown(false);
      }
      if (codeDropdownRef.current && !codeDropdownRef.current.contains(event.target as Node)) {
        setShowCodeDropdown(false);
      }
      if (skdiacodeDropdownRef.current && !skdiacodeDropdownRef.current.contains(event.target as Node)) {
        setShowSkdiacodeDropdown(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const loadMedData = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await getMedCloudData();
      console.log(':', response);

      if (response.Code === 200 && response.Data) {
        setMedData(response.Data);
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  // Filter by NAME
  const filteredByName = useMemo(() => {
    if (!medData.length || !searchName) return [];
    return medData.filter((med) =>
      med.NAME?.toLowerCase().includes(searchName.toLowerCase())
    );
  }, [medData, searchName]);

  // Filter by CHT_NAME
  const filteredByChtName = useMemo(() => {
    if (!medData.length || !searchChtName) return [];
    return medData.filter((med) =>
      med.CHT_NAME?.toLowerCase().includes(searchChtName.toLowerCase())
    );
  }, [medData, searchChtName]);

  // Filter by CODE
  const filteredByCode = useMemo(() => {
    if (!medData.length || !searchCode) return [];
    return medData.filter((med) =>
      med.CODE?.toLowerCase().includes(searchCode.toLowerCase())
    );
  }, [medData, searchCode]);

  // Filter by SKDIACODE
  const filteredBySkdiacode = useMemo(() => {
    if (!medData.length || !searchSkdiacode) return [];
    return medData.filter((med) =>
      med.SKDIACODE?.toLowerCase().includes(searchSkdiacode.toLowerCase())
    );
  }, [medData, searchSkdiacode]);

  const handleSelectMed = (med: MedData) => {
    setSelectedMed(med);
    setSearchName(med.NAME || '');
    setSearchChtName(med.CHT_NAME || '');
    setSearchCode(med.CODE || '');
    setSearchSkdiacode(med.SKDIACODE || '');
    setShowNameDropdown(false);
    setShowChtNameDropdown(false);
    setShowCodeDropdown(false);
    setShowSkdiacodeDropdown(false);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!searchCode) {
      setError('');
      return;
    }

    setSubmitting(true);
    setError(null);

    try {
      const requestData = {
        Data: [
          {
            BARCODE: [barcode],
            CODE: searchCode,
            BARCODE2: JSON.stringify([barcode])
          }
        ]
      };

      console.log(':', requestData);

      const response = await apiCall('/api/med_page/add_med_clouds', {
        method: 'POST',
        body: requestData
      });

      console.log(':', response);

      if (response.Code === 200) {
        onSuccess();
        onClose();
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError('');
    } finally {
      setSubmitting(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between p-4 border-b">
          <div className="flex items-center">
            <Plus className="w-6 h-6 text-blue-600 mr-2" />
            <h3 className="text-lg font-semibold text-gray-900"></h3>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6">
          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          {loading && (
            <div className="text-center py-8 text-gray-500">
              ...
            </div>
          )}

          {!loading && (
            <div className="space-y-4">
              {/*  */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <input
                  type="text"
                  value={barcode}
                  disabled
                  className="w-full px-3 py-2 border border-gray-300 rounded-md bg-gray-50 text-gray-500"
                />
              </div>

              {/*  -  */}
              <div ref={nameDropdownRef} className="relative">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                   <span className="text-red-500">*</span>
                </label>
                <div className="relative">
                  <input
                    type="text"
                    value={searchName}
                    onChange={(e) => {
                      setSearchName(e.target.value);
                      setShowNameDropdown(true);
                    }}
                    onFocus={() => setShowNameDropdown(true)}
                    placeholder=""
                    disabled={submitting}
                    className="w-full px-3 py-2 pr-10 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                  <ChevronDown className="absolute right-3 top-2.5 w-5 h-5 text-gray-400 pointer-events-none" />
                </div>

                {showNameDropdown && searchName && filteredByName.length > 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-64 overflow-y-auto">
                    {filteredByName.slice(0, 50).map((med, index) => (
                      <div
                        key={index}
                        onClick={() => handleSelectMed(med)}
                        className={`px-4 py-2 cursor-pointer hover:bg-blue-50 border-b border-gray-100 last:border-b-0 ${
                          selectedMed?.CODE === med.CODE ? 'bg-blue-50' : ''
                        }`}
                      >
                        <div className="text-sm font-medium text-gray-900">
                          {med.NAME}
                        </div>
                        <div className="text-xs text-gray-500 mt-1">
                          : {med.CODE} | : {med.SKDIACODE}
                        </div>
                      </div>
                    ))}
                    {filteredByName.length > 50 && (
                      <div className="px-4 py-2 text-sm text-gray-500 bg-gray-50 text-center">
                         50 
                      </div>
                    )}
                  </div>
                )}

                {showNameDropdown && searchName && filteredByName.length === 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg">
                    <div className="px-4 py-3 text-sm text-gray-500 text-center">
                      
                    </div>
                  </div>
                )}
              </div>

              {/*  -  */}
              <div ref={chtNameDropdownRef} className="relative">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <input
                    type="text"
                    value={searchChtName}
                    onChange={(e) => {
                      setSearchChtName(e.target.value);
                      setShowChtNameDropdown(true);
                    }}
                    onFocus={() => setShowChtNameDropdown(true)}
                    placeholder=""
                    disabled={submitting}
                    className="w-full px-3 py-2 pr-10 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                  <ChevronDown className="absolute right-3 top-2.5 w-5 h-5 text-gray-400 pointer-events-none" />
                </div>

                {showChtNameDropdown && searchChtName && filteredByChtName.length > 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-64 overflow-y-auto">
                    {filteredByChtName.slice(0, 50).map((med, index) => (
                      <div
                        key={index}
                        onClick={() => handleSelectMed(med)}
                        className={`px-4 py-2 cursor-pointer hover:bg-blue-50 border-b border-gray-100 last:border-b-0 ${
                          selectedMed?.CODE === med.CODE ? 'bg-blue-50' : ''
                        }`}
                      >
                        <div className="text-sm font-medium text-gray-900">
                          {med.CHT_NAME}
                        </div>
                        <div className="text-xs text-gray-500 mt-1">
                          : {med.CODE} | : {med.SKDIACODE}
                        </div>
                      </div>
                    ))}
                    {filteredByChtName.length > 50 && (
                      <div className="px-4 py-2 text-sm text-gray-500 bg-gray-50 text-center">
                         50 
                      </div>
                    )}
                  </div>
                )}

                {showChtNameDropdown && searchChtName && filteredByChtName.length === 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg">
                    <div className="px-4 py-3 text-sm text-gray-500 text-center">
                      
                    </div>
                  </div>
                )}
              </div>

              {/*  -  */}
              <div ref={codeDropdownRef} className="relative">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <input
                    type="text"
                    value={searchCode}
                    onChange={(e) => {
                      setSearchCode(e.target.value);
                      setShowCodeDropdown(true);
                    }}
                    onFocus={() => setShowCodeDropdown(true)}
                    placeholder=""
                    disabled={submitting}
                    className="w-full px-3 py-2 pr-10 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                  <ChevronDown className="absolute right-3 top-2.5 w-5 h-5 text-gray-400 pointer-events-none" />
                </div>

                {showCodeDropdown && searchCode && filteredByCode.length > 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-64 overflow-y-auto">
                    {filteredByCode.slice(0, 50).map((med, index) => (
                      <div
                        key={index}
                        onClick={() => handleSelectMed(med)}
                        className={`px-4 py-2 cursor-pointer hover:bg-blue-50 border-b border-gray-100 last:border-b-0 ${
                          selectedMed?.CODE === med.CODE ? 'bg-blue-50' : ''
                        }`}
                      >
                        <div className="text-sm font-medium text-gray-900">
                          {med.CODE}
                        </div>
                        <div className="text-xs text-gray-500 mt-1">
                          : {med.NAME} | : {med.SKDIACODE}
                        </div>
                      </div>
                    ))}
                    {filteredByCode.length > 50 && (
                      <div className="px-4 py-2 text-sm text-gray-500 bg-gray-50 text-center">
                         50 
                      </div>
                    )}
                  </div>
                )}

                {showCodeDropdown && searchCode && filteredByCode.length === 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg">
                    <div className="px-4 py-3 text-sm text-gray-500 text-center">
                      
                    </div>
                  </div>
                )}
              </div>

              {/*  -  */}
              <div ref={skdiacodeDropdownRef} className="relative">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div className="relative">
                  <input
                    type="text"
                    value={searchSkdiacode}
                    onChange={(e) => {
                      setSearchSkdiacode(e.target.value);
                      setShowSkdiacodeDropdown(true);
                    }}
                    onFocus={() => setShowSkdiacodeDropdown(true)}
                    placeholder=""
                    disabled={submitting}
                    className="w-full px-3 py-2 pr-10 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  />
                  <ChevronDown className="absolute right-3 top-2.5 w-5 h-5 text-gray-400 pointer-events-none" />
                </div>

                {showSkdiacodeDropdown && searchSkdiacode && filteredBySkdiacode.length > 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-64 overflow-y-auto">
                    {filteredBySkdiacode.slice(0, 50).map((med, index) => (
                      <div
                        key={index}
                        onClick={() => handleSelectMed(med)}
                        className={`px-4 py-2 cursor-pointer hover:bg-blue-50 border-b border-gray-100 last:border-b-0 ${
                          selectedMed?.CODE === med.CODE ? 'bg-blue-50' : ''
                        }`}
                      >
                        <div className="text-sm font-medium text-gray-900">
                          {med.SKDIACODE}
                        </div>
                        <div className="text-xs text-gray-500 mt-1">
                          : {med.NAME} | : {med.CODE}
                        </div>
                      </div>
                    ))}
                    {filteredBySkdiacode.length > 50 && (
                      <div className="px-4 py-2 text-sm text-gray-500 bg-gray-50 text-center">
                         50 
                      </div>
                    )}
                  </div>
                )}

                {showSkdiacodeDropdown && searchSkdiacode && filteredBySkdiacode.length === 0 && (
                  <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg">
                    <div className="px-4 py-3 text-sm text-gray-500 text-center">
                      
                    </div>
                  </div>
                )}
              </div>
            </div>
          )}

          <div className="flex justify-end gap-3 mt-6">
            <button
              type="button"
              onClick={onClose}
              disabled={submitting}
              className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors disabled:opacity-50"
            >
              
            </button>
            <button
              type="submit"
              disabled={submitting || !searchCode || loading}
              className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {submitting ? '...' : ''}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};
