import { loadConfig, getApiUrl,  getAiUrl} from '../config';

export interface ApiCallOptions {
  method?: string;
  headers?: Record<string, string>;
  body?: any;
}

export interface ApiMultipartOptions {
  method?: string;
  headers?: Record<string, string>;
  body?: FormData;
}

export const apiCall = async (path: string, options: ApiCallOptions = {}): Promise<any> => {
  // Ensure config is loaded
  await loadConfig();
  
  const url = getApiUrl(path);
  const { method = 'GET', headers = {}, body } = options;

  const requestOptions: RequestInit = {
    method,
    headers: {
      'Content-Type': 'application/json',
      ...headers,
    },
  };

  if (body && method !== 'GET') {
    requestOptions.body = JSON.stringify(body);
  }

  try {
    const response = await fetch(url, requestOptions);
    
    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }
    
    const data = await response.json();
    return data;
  } catch (error) {
    console.error('API call failed:', error);
    throw error;
  }
};

export const apiCallMultipart = async (
  path: string,
  options: ApiMultipartOptions
): Promise<any> => {
  await loadConfig();
  const url = getApiUrl(path);
  const { method = 'POST', headers = {}, body } = options;

  //   Content-Type boundary
  const requestOptions: RequestInit = {
    method,
    headers,     //  header Content-Type
    body,        //  FormData
  };

  try {
    const response = await fetch(url, requestOptions);

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    //  JSON 
    return await response.json();
  } catch (error) {
    console.error('API multipart call failed:', error);
    throw error;
  }
};
export const aiCallMultipart = async (
  path: string,
  options: ApiMultipartOptions
): Promise<any> => {
  await loadConfig();
  const url = getAiUrl(path);
  console.log('URL:',url);
  const { method = 'POST', headers = {}, body } = options;

  //   Content-Type boundary
  const requestOptions: RequestInit = {
    method,
    headers,     //  header Content-Type
    body,        //  FormData
  };

  try {
    console.log(':', url);
    console.log('Request options:', {
      method,
      hasBody: !!body,
      bodyType: body?.constructor?.name
    });

    const response = await fetch(url, requestOptions);

    console.log(':', {
      status: response.status,
      statusText: response.statusText,
      ok: response.ok,
      headers: Object.fromEntries(response.headers.entries())
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error('API :', errorText);
      throw new Error(`HTTP error! status: ${response.status}, body: ${errorText}`);
    }

    //  JSON 
    const result = await response.json();
    console.log('API :', result);
    return result;
  } catch (error) {
    console.error('API multipart call failed:', error);

    //  CORS 
    if (error instanceof TypeError && error.message === 'Failed to fetch') {
      console.error('  CORS ');
      console.error('');
      console.error('1.  CORS headers');
      console.error('2. URL :', url);
      console.error('3. ');
    }

    throw error;
  }
};

export interface ApiCallBlobOptions {
  method?: string;
  headers?: Record<string, string>;
  body?: any;
}

export const apiCallBlob = async (path: string, options: ApiCallBlobOptions = {}): Promise<Blob> => {
  // Ensure config is loaded
  await loadConfig();

  const url = getApiUrl(path);
  const { method = 'GET', headers = {}, body } = options;

  const requestOptions: RequestInit = {
    method,
    headers: {
      'Content-Type': 'application/json',
      ...headers,
    },
  };

  if (body && method !== 'GET') {
    requestOptions.body = JSON.stringify(body);
  }

  try {
    const response = await fetch(url, requestOptions);

    if (!response.ok) {
      throw new Error(`HTTP error! status: ${response.status}`);
    }

    const blob = await response.blob();
    return blob;
  } catch (error) {
    console.error('API blob call failed:', error);
    throw error;
  }
};