export const getDefaultDateRange = (): [string, string] => {
  const now = new Date();
  const oneMonthAgo = new Date();
  oneMonthAgo.setMonth(now.getMonth() - 1);

  const formatDate = (date: Date, isEndDate = false): string => {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    
    if (isEndDate) {
      return `${year}-${month}-${day} 23:59:59`;
    } else {
      return `${year}-${month}-${day} 00:00:00`;
    }
  };

  return [formatDate(oneMonthAgo), formatDate(now, true)];
};

export const groupInspectionsByDrug = (inspections: any[], showLockedData: boolean = false): GroupedInspection[] => {
  const grouped = new Map<string, GroupedInspection>();

  // 
  const filteredInspections = showLockedData 
    ? inspections 
    : inspections.filter(item => {
        // 
        if (!item.Sub_content || item.Sub_content.length === 0) {
          return true;
        }
        // 
        return !item.Sub_content.some((sub: any) => sub.STATE === '');
      });

  filteredInspections.forEach(item => {
    const key = `${item.CODE}-${item.SKDIACODE}`;
    
    // 
    const receivedQty = item.Sub_content?.reduce((sum: number, sub: any) => {
      return sum + (parseInt(sub.END_QTY) || 0);
    }, 0) || 0;
    
    if (grouped.has(key)) {
      const existing = grouped.get(key)!;
      existing.totalStartQty += parseInt(item.START_QTY) || 0;
      existing.totalReceivedQty += receivedQty;
      existing.items.push(item);
    } else {
      grouped.set(key, {
        CODE: item.CODE,
        SKDIACODE: item.SKDIACODE,
        CHT_NAME: item.CHT_NAME,
        NAME: item.NAME,
        BRD: item.BRD,
        PAKAGE: item.PAKAGE,
        totalStartQty: parseInt(item.START_QTY) || 0,
        totalReceivedQty: receivedQty,
        items: [item]
      });
    }
  });

  return Array.from(grouped.values()).sort((a, b) => a.CODE.localeCompare(b.CODE));
};

