export const updateSubContent = async (
  subContentData: any
): Promise<UpdateSubContentResponse> => {
  //  OP_TIME 
  const now = new Date();
  const formattedTime = now.getFullYear() + '-' + 
    String(now.getMonth() + 1).padStart(2, '0') + '-' + 
    String(now.getDate()).padStart(2, '0') + ' ' + 
    String(now.getHours()).padStart(2, '0') + ':' + 
    String(now.getMinutes()).padStart(2, '0') + ':' + 
    String(now.getSeconds()).padStart(2, '0');

  const requestData: UpdateSubContentRequest = {
    Data: [{
      ...subContentData,
      OP_TIME: formattedTime
    }]
  };

  // console
  console.log('=== Update API Request Debug ===');
  console.log('URL:', '/api/inspection/sub_content_update');
  console.log('Method:', 'POST');
  console.log('Request Data:', JSON.stringify(requestData, null, 2));
  console.log('================================');

  const response = await apiCall('/api/inspection/sub_content_update', {
    method: 'POST',
    body: requestData,
  });

  await triggerWebSocketBroadcast(response);

  return response;
};

export const analyzeShippingDocument = async (
  inspectionItem: any,
  base64Image: string,
  opId: string,
  opName: string
): Promise<TextVisionResponse> => {
  const requestData: TextVisionRequest = {
    Data: {
      GUID: inspectionItem.GUID,
      Master_GUID: inspectionItem.Master_GUID,
      PON: inspectionItem.PON,
      IC_SN: inspectionItem.IC_SN,
      CODE: inspectionItem.CODE,
      NAME: inspectionItem.NAME,
      BRD: inspectionItem.BRD || '',
      SKDIACODE: inspectionItem.SKDIACODE,
      BARCODE1: inspectionItem.BARCODE1 || '',
      BARCODE2: inspectionItem.BARCODE2 || '',
      START_QTY: inspectionItem.START_QTY,
      ADD_TIME: inspectionItem.ADD_TIME,
      SEQ: inspectionItem.SEQ || '',
      FREE_CHARGE_FLAG: inspectionItem.FREE_CHARGE_FLAG || '',
      API_RETURN_NOTE: inspectionItem.API_RETURN_NOTE || '',
      NOTE: inspectionItem.NOTE || '',
      CHT_NAME: inspectionItem.CHT_NAME,
      PAKAGE: inspectionItem.PAKAGE,
      END_QTY: inspectionItem.END_QTY || inspectionItem.START_QTY,
      textVision: {
        op_id: opId,
        op_name: opName,
        base64: base64Image
      }
    }
  };

  // console
  console.log('=== Text Vision API Request Debug ===');
  console.log('URL:', '/api/pcmpo/analyze_single');
  console.log('Method:', 'POST');
  console.log('Request Data:', JSON.stringify(requestData, null, 2));
  console.log('=====================================');

  return await apiCall('/api/pcmpo/analyze_single', {
    method: 'POST',
    body: requestData,
  });
};

