import React, { useState, useRef } from 'react';
import { Upload, X, Image, FileText, AlertCircle, CheckCircle, Trash2, Eye, FileSpreadsheet } from 'lucide-react';
import { getLoggedID, getLoggedName } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';
import { apiCall } from '../../utils/api';
import { BatchAnalysisResultModal } from './BatchAnalysisResultModal';
import { uploadSubContentExcel } from '../../services/inspection';
import { useToast, ToastContainer } from '../common/Toast';

interface BatchUploadModalProps {
  isOpen: boolean;
  onClose: () => void;
  onDataUpdate?: () => void;
}

interface UploadedImage {
  id: string;
  file: File;
  previewUrl: string;
  guid?: string;
  status: 'pending' | 'uploading' | 'uploaded' | 'analyzing' | 'analyzed' | 'error';
  error?: string;
  analysisResult?: any;
}

interface PreSaveRequest {
  Data: Array<{
    op_id: string;
    op_name: string;
    base64: string;
  }>;
}

interface PreSaveResponse {
  Code: number;
  Result: string;
  Data?: Array<{
    GUID: string;
    [key: string]: any;
  }>;
}

interface AnalyzeBatchRequest {
  ValueAry: [string];
}

interface AnalyzeBatchResponse {
  Code: number;
  Result: string;
  Data?: any[];
}

export const BatchUploadModal: React.FC<BatchUploadModalProps> = ({
  isOpen,
  onClose,
  onDataUpdate,
}) => {
  const [images, setImages] = useState<UploadedImage[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [analysisResults, setAnalysisResults] = useState<any[]>([]);
  const [resultModalOpen, setResultModalOpen] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const excelInputRef = useRef<HTMLInputElement>(null);
  const [excelUploading, setExcelUploading] = useState(false);
  const { toasts, showToast, removeToast } = useToast();

  // 
  const resetForm = () => {
    setImages([]);
    setError(null);
    setAnalysisResults([]);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const files = Array.from(event.target.files || []);
    if (files.length === 0) return;

    const newImages: UploadedImage[] = files.map((file, index) => {
      // 
      if (!file.type.startsWith('image/')) {
        return {
          id: `${Date.now()}-${index}`,
          file,
          previewUrl: '',
          status: 'error',
          error: ''
        };
      }

      //  ( 10MB)
      if (file.size > 10 * 1024 * 1024) {
        return {
          id: `${Date.now()}-${index}`,
          file,
          previewUrl: '',
          status: 'error',
          error: ' 10MB'
        };
      }

      return {
        id: `${Date.now()}-${index}`,
        file,
        previewUrl: '',
        status: 'pending'
      };
    });

    // 
    newImages.forEach((imageItem) => {
      if (imageItem.status === 'pending') {
        const reader = new FileReader();
        reader.onload = (e) => {
          setImages(prev => prev.map(img => 
            img.id === imageItem.id 
              ? { ...img, previewUrl: e.target?.result as string }
              : img
          ));
        };
        reader.readAsDataURL(imageItem.file);
      }
    });

    setImages(prev => [...prev, ...newImages]);
    setError(null);
  };

  // 
  const removeImage = (id: string) => {
    setImages(prev => prev.filter(img => img.id !== id));
  };

  //  BASE64
  const convertToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        const result = reader.result as string;
        const base64 = result.split(',')[1];
        resolve(base64);
      };
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  //  -  GUID
  const uploadSingleImage = async (imageItem: UploadedImage): Promise<string | null> => {
    const opId = getLoggedID();
    const opName = getLoggedName();

    if (!opId || !opName) {
      throw new Error('');
    }

    try {
      // 
      setImages(prev => prev.map(img => 
        img.id === imageItem.id 
          ? { ...img, status: 'uploading' }
          : img
      ));

      const base64Image = await convertToBase64(imageItem.file);

      const requestData: PreSaveRequest = {
        Data: [{
          op_id: opId,
          op_name: opName,
          base64: base64Image
        }]
      };

      console.log('=== PreSave API Request Debug ===');
      console.log('File:', imageItem.file.name);
      console.log('Request Data:', JSON.stringify(requestData, null, 2));

      const response: PreSaveResponse = await apiCall('/api/pcmpo/preSave', {
        method: 'POST',
        body: requestData,
      });

      console.log('PreSave Response:', JSON.stringify(response, null, 2));

      if (response.Code === 200 && response.Data && response.Data.length > 0) {
        const guid = response.Data[0].GUID;
        
        // 
        setImages(prev => prev.map(img => 
          img.id === imageItem.id 
            ? { ...img, status: 'uploaded', guid }
            : img
        ));

        return guid;
      } else {
        throw new Error(response.Result || '');
      }
    } catch (err) {
      console.error('Upload error:', err);
      
      // 
      setImages(prev => prev.map(img => 
        img.id === imageItem.id 
          ? { ...img, status: 'error', error: err instanceof Error ? err.message : '' }
          : img
      ));

      throw err; // 
    }
  };

  // 
  const analyzeSingleImage = async (imageItem: UploadedImage): Promise<any> => {
    if (!imageItem.guid) {
      throw new Error(' GUID');
    }

    try {
      console.log(`=== : ${imageItem.file.name} ===`);
      console.log(' GUID:', imageItem.guid);
      
      // 
      setImages(prev => prev.map(img => 
        img.id === imageItem.id 
          ? { ...img, status: 'analyzing' }
          : img
      ));

      const requestData: AnalyzeBatchRequest = {
        ValueAry: [imageItem.guid]
      };

      console.log('=== Analyze Batch API Request Debug ===');
      console.log('API URL:', '/api/pcmpo/analyze_batch');
      console.log('Method:', 'POST');
      console.log('Request Data:', JSON.stringify(requestData, null, 2));
      console.log(' API ...');

      const response: AnalyzeBatchResponse = await apiCall('/api/pcmpo/analyze_batch', {
        method: 'POST',
        body: requestData,
      });

      console.log('API ...');
      console.log('Analyze Batch Response:', JSON.stringify(response, null, 2));

      if (response.Code === 200 && response.Data) {
        console.log(`  ${imageItem.file.name} `);
        // 
        setImages(prev => prev.map(img => 
          img.id === imageItem.id 
            ? { ...img, status: 'analyzed', analysisResult: response.Data }
            : img
        ));

        return response.Data;
      } else {
        console.log(`  ${imageItem.file.name} :`, response.Result);
        throw new Error(response.Result || '');
      }
    } catch (err) {
      console.error(`  ${imageItem.file.name} :`, err);
      
      // 
      setImages(prev => prev.map(img => 
        img.id === imageItem.id 
          ? { ...img, status: 'error', error: err instanceof Error ? err.message : '' }
          : img
      ));

      throw err;
    }
  };

  // 
  const handleBatchProcess = async () => {
    const validImages = images.filter(img => img.status === 'pending' && img.previewUrl);
    
    if (validImages.length === 0) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      console.log('===  ===');
      console.log(':', validImages.length);

      //  imageId -> guid 
      console.log('');
      const uploadResults: Array<{ imageId: string, guid: string, imageItem: UploadedImage }> = [];
      
      for (const imageItem of validImages) {
        try {
          const guid = await uploadSingleImage(imageItem);
          if (guid) {
            uploadResults.push({ 
              imageId: imageItem.id, 
              guid, 
              imageItem 
            });
            console.log(`  ${imageItem.file.name} GUID: ${guid}`);
          }
        } catch (error) {
          console.error(`  ${imageItem.file.name} :`, error);
        }
      }
      
      console.log(':', uploadResults.length);
      console.log(':', uploadResults.map(r => ({ 
        fileName: r.imageItem.file.name, 
        guid: r.guid 
      })));

      if (uploadResults.length === 0) {
        setError('');
        return;
      }

      // 
      console.log('');
      console.log(':', uploadResults.length);
      console.log('GUIDs:', uploadResults.map(r => r.guid));
      
      const allAnalysisResults: any[] = [];
      
      for (const uploadResult of uploadResults) {
        const { imageItem, guid } = uploadResult;
        console.log(`: ${imageItem.file.name}, GUID: ${guid}`);
        
        try {
          // UI
          setImages(prev => prev.map(img => 
            img.id === imageItem.id 
              ? { ...img, status: 'analyzing' }
              : img
          ));

          const requestData: AnalyzeBatchRequest = {
            ValueAry: [guid]
          };

          console.log('=== Analyze Batch API Request Debug ===');
          console.log('API URL:', '/api/pcmpo/analyze_batch');
          console.log('Method:', 'POST');
          console.log('Request Data:', JSON.stringify(requestData, null, 2));
          console.log(' API ...');

          const response: AnalyzeBatchResponse = await apiCall('/api/pcmpo/analyze_batch', {
            method: 'POST',
            body: requestData,
          });

          console.log('API ...');
          console.log('Analyze Batch Response:', JSON.stringify(response, null, 2));

          if (response.Code === 200 && response.Data) {
            console.log(`  ${imageItem.file.name} `);
            
            // UI
            setImages(prev => prev.map(img => 
              img.id === imageItem.id 
                ? { ...img, status: 'analyzed', analysisResult: response.Data }
                : img
            ));

            // 
            if (Array.isArray(response.Data)) {
              allAnalysisResults.push(...response.Data);
            } else {
              allAnalysisResults.push(response.Data);
              
              // 
              if (response.Data) {
                if (Array.isArray(response.Data)) {
                  allAnalysisResults.push(...response.Data);
                } else {
                  allAnalysisResults.push(response.Data);
                }
              } else {
                //  Data
                allAnalysisResults.push({
                  GUID: guid,
                  Result: response.Result || '',
                  check: '',
                  base64: imageItem.previewUrl, // 
                  op_name: getLoggedName(),
                  op_time: new Date().toLocaleString('zh-TW'),
                  po_num: '',
                  qty: '',
                  batch_num: '',
                  expirydate: '',
                  name: '',
                  cht_name: '',
                  code: ''
                });
              }
            }
          } else {
            console.log(`  ${imageItem.file.name} :`, response.Result);
            
            // UI
            setImages(prev => prev.map(img => 
              img.id === imageItem.id 
                ? { ...img, status: 'error', error: response.Result || '' }
                : img
            ));

            // 
            allAnalysisResults.push({
              GUID: guid,
              Result: response.Result || '',
              check: '',
              base64: imageItem.previewUrl, // 
              op_name: getLoggedName(),
              create_time: new Date().toLocaleString('zh-TW'),
              po_num: '',
              qty: '',
              batch_num: '',
              expirydate: '',
              name: '',
              cht_name: '',
              code: '',
              // 
              _isFailed: true,
              _failureReason: response.Result || ''
            });
          }
        } catch (error) {
          console.error(`  ${imageItem.file.name} :`, error);
          
          // UI
          setImages(prev => prev.map(img => 
            img.id === imageItem.id 
              ? { ...img, status: 'error', error: error instanceof Error ? error.message : '' }
              : img
          ));

          // 
          allAnalysisResults.push({
            GUID: guid,
            Result: error instanceof Error ? error.message : '',
            check: '',
            base64: imageItem.previewUrl, // 
            op_name: getLoggedName(),
            create_time: new Date().toLocaleString('zh-TW'),
            po_num: '',
            qty: '',
            batch_num: '',
            expirydate: '',
            name: '',
            cht_name: '',
            code: '',
            // 
            _isFailed: true,
            _failureReason: error instanceof Error ? error.message : ''
          });
        }
      }
      
      console.log(':', allAnalysisResults.length);
      console.log(':', allAnalysisResults);
      console.log('===  ===');

      if (allAnalysisResults.length > 0) {
        setAnalysisResults(allAnalysisResults);
        setResultModalOpen(true);
      } else {
        setError('');
      }

    } catch (err) {
      console.error('Batch process error:', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  //  Excel 
  const handleExcelUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // 
    if (!file.name.endsWith('.xlsx') && !file.name.endsWith('.xls')) {
      showToast(' Excel  (.xlsx  .xls)', 'error');
      if (excelInputRef.current) {
        excelInputRef.current.value = '';
      }
      return;
    }

    const ct = getLoggedName();
    if (!ct) {
      showToast('', 'error');
      return;
    }

    setExcelUploading(true);
    setError(null);

    try {
      console.log('===  Excel ===');
      console.log(':', file.name);
      console.log(':', ct);

      const response = await uploadSubContentExcel(file, ct);

      console.log(':', response);

      if (response.Code === 200) {
        //  Toast  API  Result
        showToast(response.Result || 'Excel ', 'success');

        // 
        if (onDataUpdate) {
          onDataUpdate();
        }

        // 
        if (excelInputRef.current) {
          excelInputRef.current.value = '';
        }

        // 
        setTimeout(() => {
          handleClose();
        }, 500);
      } else {
        //  Toast
        showToast(response.Result || 'Excel ', 'error');
      }
    } catch (err) {
      console.error('Excel :', err);
      const errorMessage = err instanceof Error ? err.message : 'Excel ';
      showToast(errorMessage, 'error');
    } finally {
      setExcelUploading(false);
    }
  };

  // 
  const getStatusDisplay = (status: UploadedImage['status'], error?: string) => {
    switch (status) {
      case 'pending':
        return <span className="text-gray-500"></span>;
      case 'uploading':
        return <span className="text-blue-500 flex items-center"><LoadingSpinner size="small" className="mr-1" /></span>;
      case 'uploaded':
        return <span className="text-green-500 flex items-center"><CheckCircle className="w-4 h-4 mr-1" /></span>;
      case 'analyzing':
        return <span className="text-blue-500 flex items-center"><LoadingSpinner size="small" className="mr-1" /></span>;
      case 'analyzed':
        return <span className="text-green-500 flex items-center"><CheckCircle className="w-4 h-4 mr-1" /></span>;
      case 'error':
        return <span className="text-red-500 flex items-center"><AlertCircle className="w-4 h-4 mr-1" />{error || ''}</span>;
      default:
        return null;
    }
  };

  if (!isOpen) return null;

  return (
    <>
      <ToastContainer toasts={toasts} onRemove={removeToast} />
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
        <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-hidden">
          <div className="flex items-center justify-between p-6 border-b">
            <div className="flex-1">
              <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                <Upload className="w-5 h-5 mr-2" />
                
              </h3>
              <p className="text-sm text-gray-600 mt-1">
                
              </p>
            </div>

            <div className="flex items-center gap-3">
              {/* Excel  */}
              <div>
                <input
                  ref={excelInputRef}
                  type="file"
                  accept=".xlsx,.xls"
                  onChange={handleExcelUpload}
                  className="hidden"
                />
                <button
                  onClick={() => excelInputRef.current?.click()}
                  disabled={excelUploading || loading}
                  className="inline-flex items-center px-3 py-2 text-sm font-medium rounded-md bg-blue-600 text-white hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                  title=" Excel"
                >
                  {excelUploading ? (
                    <>
                      <LoadingSpinner size="small" className="mr-2" />
                      ...
                    </>
                  ) : (
                    <>
                      <FileSpreadsheet className="w-4 h-4 mr-2" />
                       Excel
                    </>
                  )}
                </button>
              </div>

              <button
                onClick={handleClose}
                className="text-gray-400 hover:text-gray-600 transition-colors"
              >
                <X className="w-6 h-6" />
              </button>
            </div>
          </div>

          <div className="p-6 overflow-y-auto max-h-[calc(90vh-200px)]">
            {error && (
              <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
                <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
                {error}
              </div>
            )}

            <div className="space-y-4">
              {/*  */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div
                  className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-gray-400 transition-colors cursor-pointer"
                  onClick={() => fileInputRef.current?.click()}
                >
                  <input
                    ref={fileInputRef}
                    type="file"
                    accept="image/*"
                    multiple
                    onChange={handleFileSelect}
                    className="hidden"
                  />
                  
                  <div className="space-y-2">
                    <Image className="w-12 h-12 text-gray-400 mx-auto" />
                    <div>
                      <p className="text-sm text-gray-600"></p>
                      <p className="text-xs text-gray-500"> JPG, PNG, GIF </p>
                    </div>
                  </div>
                </div>
              </div>

              {/*  */}
              {images.length > 0 && (
                <div>
                  <h4 className="text-sm font-medium text-gray-700 mb-3">
                     ({images.length} )
                  </h4>
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {images.map((imageItem) => (
                      <div key={imageItem.id} className="border rounded-lg p-3">
                        <div className="flex items-start justify-between mb-2">
                          <div className="flex-1 min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {imageItem.file.name}
                            </p>
                            <p className="text-xs text-gray-500">
                              {(imageItem.file.size / 1024 / 1024).toFixed(2)} MB
                            </p>
                          </div>
                          <button
                            onClick={() => removeImage(imageItem.id)}
                            className="ml-2 text-red-500 hover:text-red-700"
                            disabled={loading}
                          >
                            <Trash2 className="w-4 h-4" />
                          </button>
                        </div>
                        
                        {imageItem.previewUrl && (
                          <img
                            src={imageItem.previewUrl}
                            alt=""
                            className="w-full h-32 object-cover rounded mb-2"
                          />
                        )}
                        
                        <div className="text-xs">
                          {getStatusDisplay(imageItem.status, imageItem.error)}
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>

          <div className="flex justify-between items-center p-6 border-t bg-gray-50">
            <div className="text-sm text-gray-600">
              {images.length > 0 && (
                <span>
                   {images.length}  | 
                  : {images.filter(img => img.status === 'pending').length} | 
                  : {images.filter(img => img.status === 'analyzed').length} | 
                  : {images.filter(img => img.status === 'error').length}
                </span>
              )}
            </div>
            
            <div className="flex gap-3">
              <button
                type="button"
                onClick={handleClose}
                className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
                disabled={loading}
              >
                
              </button>
              <button
                onClick={handleBatchProcess}
                disabled={loading || images.filter(img => img.status === 'pending').length === 0}
                className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center"
              >
                {loading ? (
                  <>
                    <LoadingSpinner size="small" className="mr-2" />
                    ...
                  </>
                ) : (
                  <>
                    <Upload className="w-4 h-4 mr-2" />
                    
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <BatchAnalysisResultModal
        isOpen={resultModalOpen}
        onClose={() => {
          setResultModalOpen(false);
          setAnalysisResults([]);
          handleClose();
        }}
        results={analysisResults}
        onDataUpdate={onDataUpdate}
      />
    </>
  );
};