import React, { useState } from 'react';
import { Calendar, Search, RotateCcw, Clock, ChevronDown } from 'lucide-react';
import { useLanguage } from '../../contexts/LanguageContext';

interface DateRangeFilterProps {
  startDate: string;
  endDate: string;
  onDateChange: (startDate: string, endDate: string) => void;
  onSearch: () => void;
  onReset: () => void;
  loading: boolean;
}

export const DateRangeFilter: React.FC<DateRangeFilterProps> = ({
  startDate,
  endDate,
  onDateChange,
  onSearch,
  onReset,
  loading,
}) => {
  const { t } = useLanguage();
  const [isExpanded, setIsExpanded] = useState(false);
  const [timeType, setTimeType] = useState(t('app.inspection.operation_time'));
  const [localStartDate, setLocalStartDate] = useState(startDate.split(' ')[0]);
  const [localStartTime, setLocalStartTime] = useState('00:00:00');
  const [localEndDate, setLocalEndDate] = useState(endDate.split(' ')[0]);
  const [localEndTime, setLocalEndTime] = useState('23:59:59');

  const timeTypeOptions = [
    t('app.inspection.operation_time'),
    t('app.inspection.add_time'),
    t('app.inspection.update_time')
  ];

  const handleSearch = () => {
    const formattedStartDate = `${localStartDate} ${localStartTime}`;
    const formattedEndDate = `${localEndDate} ${localEndTime}`;
    onDateChange(formattedStartDate, formattedEndDate);
    onSearch();
  };

  const handleReset = () => {
    onReset();
    setLocalStartDate(startDate.split(' ')[0]);
    setLocalEndDate(endDate.split(' ')[0]);
    setLocalStartTime('00:00:00');
    setLocalEndTime('23:59:59');
    setTimeType(t('app.inspection.operation_time'));
  };

  return (
    <div className="bg-white rounded-lg shadow-sm border mb-6">
      {/*  */}
      <div 
        className="flex items-center justify-between p-4 cursor-pointer hover:bg-gray-50 transition-colors"
        onClick={() => setIsExpanded(!isExpanded)}
      >
        <div className="flex items-center gap-2">
          <Clock className="w-5 h-5 text-gray-600" />
          <h3 className="text-lg font-medium text-gray-900">{t('app.inspection.time_range')}</h3>
        </div>
        <ChevronDown 
          className={`w-5 h-5 text-gray-400 transition-transform duration-200 ${
            isExpanded ? 'rotate-180' : ''
          }`} 
        />
      </div>

      {/*  */}
      {isExpanded && (
        <div className="px-4 pb-4 border-t border-gray-100">
          <div className="grid grid-cols-1 lg:grid-cols-12 gap-4 items-end mt-4">
            {/*  */}
            <div className="lg:col-span-3">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                {t('app.inspection.time_type')}
              </label>
              <div className="relative">
                <select
                  value={timeType}
                  onChange={(e) => setTimeType(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500 bg-white appearance-none"
                >
                  {timeTypeOptions.map((option) => (
                    <option key={option} value={option}>
                      {option}
                    </option>
                  ))}
                </select>
                <ChevronDown className="absolute right-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400 pointer-events-none" />
              </div>
            </div>

            {/*  */}
            <div className="lg:col-span-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                {t('app.inspection.start_datetime')}
              </label>
              <div className="flex gap-3">
                <input
                  type="date"
                  value={localStartDate}
                  onChange={(e) => setLocalStartDate(e.target.value)}
                  className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
                <input
                  type="time"
                  value={localStartTime}
                  onChange={(e) => setLocalStartTime(e.target.value)}
                  className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  step="1"
                />
              </div>
            </div>

            {/*  */}
            <div className="lg:col-span-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                {t('app.inspection.end_datetime')}
              </label>
              <div className="flex gap-3">
                <input
                  type="date"
                  value={localEndDate}
                  onChange={(e) => setLocalEndDate(e.target.value)}
                  className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                />
                <input
                  type="time"
                  value={localEndTime}
                  onChange={(e) => setLocalEndTime(e.target.value)}
                  className="px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  step="1"
                />
              </div>
            </div>

            {/*  */}
            <div className="lg:col-span-1">
              <button
                onClick={handleSearch}
                disabled={loading}
                className="w-full inline-flex items-center justify-center px-3 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                <Search className="w-4 h-4" />
                <span className="ml-1 hidden xl:inline">{loading ? t('app.inspection.searching') : t('app.inspection.search')}</span>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};