import React, { useState } from 'react';
import { Plus, X, Calculator } from 'lucide-react';
import { InspectionContent } from '../../types/inspection';
import { addSubContent } from '../../services/inspection';
import { getLoggedName } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';

interface AddSubContentModalProps {
  isOpen: boolean;
  onClose: () => void;
  inspectionItem: InspectionContent | null;
  onSuccess: () => void;
}

export const AddSubContentModal: React.FC<AddSubContentModalProps> = ({
  isOpen,
  onClose,
  inspectionItem,
  onSuccess,
}) => {
  const [formData, setFormData] = useState({
    lot: '',
    endQty: '',
    val: '',
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [calculatorDisplay, setCalculatorDisplay] = useState('');
  const [calculatorOperation, setCalculatorOperation] = useState<string | null>(null);
  const [calculatorPrevValue, setCalculatorPrevValue] = useState<number | null>(null);

  // 
  const getCurrentReceivedTotal = (): number => {
    if (!inspectionItem?.Sub_content) return 0;
    return inspectionItem.Sub_content.reduce((sum, sub) => {
      return sum + (parseInt(sub.END_QTY) || 0);
    }, 0);
  };

  // 
  const getRemainingQuantity = (): number => {
    const startQty = parseInt(inspectionItem?.START_QTY || '0');
    const currentTotal = getCurrentReceivedTotal();
    return Math.max(0, startQty - currentTotal);
  };

  // 
  const resetForm = () => {
    setFormData({
      lot: '',
      endQty: getRemainingQuantity().toString(),
      val: '',
    });
    setError(null);
    setCalculatorDisplay('');
    setCalculatorOperation(null);
    setCalculatorPrevValue(null);
  };

  //  inspectionItem 
  React.useEffect(() => {
    if (inspectionItem) {
      const remainingQty = getRemainingQuantity();
      setFormData(prev => ({
        ...prev,
        endQty: remainingQty.toString(),
      }));
    }
  }, [inspectionItem, getCurrentReceivedTotal()]);

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleCalculatorNumber = (num: string) => {
    setCalculatorDisplay(prev => prev + num);
  };

  const handleCalculatorOperation = (op: string) => {
    if (calculatorDisplay) {
      if (calculatorPrevValue !== null && calculatorOperation) {
        handleCalculatorEquals();
      } else {
        setCalculatorPrevValue(parseFloat(calculatorDisplay));
      }
      setCalculatorOperation(op);
      setCalculatorDisplay('');
    }
  };

  const handleCalculatorEquals = () => {
    if (calculatorPrevValue !== null && calculatorOperation && calculatorDisplay) {
      const current = parseFloat(calculatorDisplay);
      let result = 0;
      
      switch (calculatorOperation) {
        case '+':
          result = calculatorPrevValue + current;
          break;
        case '-':
          result = calculatorPrevValue - current;
          break;
        case '*':
          result = calculatorPrevValue * current;
          break;
        case '/':
          result = calculatorPrevValue / current;
          break;
        default:
          return;
      }
      
      setCalculatorDisplay(result.toString());
      setCalculatorOperation(null);
      setCalculatorPrevValue(null);
    }
  };

  const handleCalculatorClear = () => {
    setCalculatorDisplay('');
    setCalculatorOperation(null);
    setCalculatorPrevValue(null);
  };

  const handleCalculatorConfirm = () => {
    if (calculatorDisplay) {
      setFormData(prev => ({ ...prev, endQty: calculatorDisplay }));
    }
  };

  // 
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!inspectionItem) return;

    // 
    const inputQty = parseInt(formData.endQty) || 0;
    const remainingQty = getRemainingQuantity();
    
    if (inputQty > remainingQty) {
      setError(` ${remainingQty}`);
      return;
    }

    const loggedName = getLoggedName();
    if (!loggedName) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const response = await addSubContent(
        inspectionItem.GUID,
        formData.lot,
        formData.endQty,
        formData.val,
        loggedName
      );

      if (response.Code === 200) {
        onSuccess();
        handleClose();
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error('Add sub content error:', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen || !inspectionItem) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between p-6 border-b">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <Plus className="w-5 h-5 mr-2" />
              
            </h3>
            <p className="text-sm text-gray-600 mt-1">
              {inspectionItem.CHT_NAME || inspectionItem.NAME}
            </p>
          </div>
          <button
            onClick={handleClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6">
          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          <div className="space-y-4">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <input
                type="text"
                value={formData.lot}
                onChange={(e) => setFormData({ ...formData, lot: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                required
                placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <input
                type="date"
                value={formData.val}
                onChange={(e) => setFormData({ ...formData, val: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                required
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <div className="relative">
                <input
                  type="number"
                  value={formData.endQty}
                  onChange={(e) => setFormData({ ...formData, endQty: e.target.value })}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  required
                  min="0"
                  step="1"
                  placeholder=""
                />
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <span className="text-gray-500 text-sm">{inspectionItem.PAKAGE}</span>
                </div>
              </div>
              <p className="text-xs text-gray-500 mt-1">
                : {inspectionItem.START_QTY} {inspectionItem.PAKAGE}
              </p>
              <p className="text-xs text-blue-600 mt-1">
                : {getCurrentReceivedTotal()} {inspectionItem.PAKAGE} | 
                : {getRemainingQuantity()} {inspectionItem.PAKAGE}
              </p>
              
              {/*  */}
              <div className="mt-3 p-3 bg-gray-50 rounded-lg">
                <div className="flex items-center justify-between mb-2">
                  <span className="text-sm font-medium text-gray-700"></span>
                  <div className="flex-1 mx-3">
                    <input
                      type="text"
                      value={calculatorDisplay}
                      readOnly
                      className="w-full px-2 py-1 text-right border border-gray-300 rounded bg-white text-sm"
                      placeholder="0"
                    />
                  </div>
                  <button
                    type="button"
                    onClick={handleCalculatorConfirm}
                    disabled={!calculatorDisplay}
                    className="px-2 py-1 bg-green-600 text-white rounded text-xs hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    
                  </button>
                </div>
                
                <div className="grid grid-cols-4 gap-1">
                  {/*  */}
                  <button type="button" onClick={handleCalculatorClear} className="p-2 bg-red-500 text-white rounded text-sm hover:bg-red-600">C</button>
                  <button type="button" onClick={() => setCalculatorDisplay(prev => prev.slice(0, -1))} className="p-2 bg-gray-300 text-gray-700 rounded text-sm hover:bg-gray-400"></button>
                  <button type="button" onClick={() => handleCalculatorOperation('/')} className="p-2 bg-blue-500 text-white rounded text-sm hover:bg-blue-600"></button>
                  <button type="button" onClick={() => handleCalculatorOperation('*')} className="p-2 bg-blue-500 text-white rounded text-sm hover:bg-blue-600"></button>
                  
                  {/*  */}
                  <button type="button" onClick={() => handleCalculatorNumber('7')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">7</button>
                  <button type="button" onClick={() => handleCalculatorNumber('8')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">8</button>
                  <button type="button" onClick={() => handleCalculatorNumber('9')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">9</button>
                  <button type="button" onClick={() => handleCalculatorOperation('-')} className="p-2 bg-blue-500 text-white rounded text-sm hover:bg-blue-600">-</button>
                  
                  {/*  */}
                  <button type="button" onClick={() => handleCalculatorNumber('4')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">4</button>
                  <button type="button" onClick={() => handleCalculatorNumber('5')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">5</button>
                  <button type="button" onClick={() => handleCalculatorNumber('6')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">6</button>
                  <button type="button" onClick={() => handleCalculatorOperation('+')} className="p-2 bg-blue-500 text-white rounded text-sm hover:bg-blue-600">+</button>
                  
                  {/*  */}
                  <button type="button" onClick={() => handleCalculatorNumber('1')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">1</button>
                  <button type="button" onClick={() => handleCalculatorNumber('2')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">2</button>
                  <button type="button" onClick={() => handleCalculatorNumber('3')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">3</button>
                  <button type="button" onClick={handleCalculatorEquals} className="p-2 bg-green-500 text-white rounded text-sm hover:bg-green-600 row-span-2">=</button>
                  
                  {/*  */}
                  <button type="button" onClick={() => handleCalculatorNumber('0')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50 col-span-2">0</button>
                  <button type="button" onClick={() => handleCalculatorNumber('.')} className="p-2 bg-white border border-gray-300 rounded text-sm hover:bg-gray-50">.</button>
                </div>
              </div>
            </div>
          </div>

          <div className="flex gap-3 mt-6">
            <button
              type="button"
              onClick={handleClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              disabled={loading}
            >
              
            </button>
            <button
              type="submit"
              disabled={loading}
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
            >
              {loading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                ''
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};