import React from 'react';
import { GroupedInspection } from '../../types/inspection';
import { Package, ChevronRight, Building2, Hash, Pill, Calendar } from 'lucide-react';
import { useLanguage } from '../../contexts/LanguageContext';

interface InspectionTableProps {
  data: GroupedInspection[];
  loading: boolean;
  onRowClick: (group: GroupedInspection) => void;
}

export const InspectionTable: React.FC<InspectionTableProps> = ({ 
  data, 
  loading, 
  onRowClick 
}) => {
  const { t } = useLanguage();

  // 
  const formatArrivalTime = (deliveryTime: string): string => {
    if (!deliveryTime) return '';
    
    try {
      // 
      const deliveryDate = new Date(deliveryTime);
      const today = new Date();
      
      // 
      deliveryDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);
      
      // 
      const timeDiff = deliveryDate.getTime() - today.getTime();
      const daysDiff = Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
      
      // 
      const formattedDate = deliveryDate.toLocaleDateString('zh-TW', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      }).replace(/\//g, '/');
      
      // 
      if (daysDiff === 0) {
        return `${formattedDate} (${t('delivery.today')})`;
      } else if (daysDiff < 0) {
        return `${formattedDate} (${t('delivery.overdue')} ${Math.abs(daysDiff)} ${t('delivery.days')})`;
      } else {
        return `${formattedDate} (${t('delivery.remaining')} ${daysDiff} ${t('delivery.days')})`;
      }
    } catch (error) {
      console.error('Error formatting arrival time:', error);
      return deliveryTime;
    }
  };

  // 
  const getArrivalTimeColor = (deliveryTime: string): string => {
    if (!deliveryTime) return 'text-gray-500';
    
    try {
      const deliveryDate = new Date(deliveryTime);
      const today = new Date();
      
      deliveryDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);
      
      const timeDiff = deliveryDate.getTime() - today.getTime();
      const daysDiff = Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
      
      if (daysDiff === 0) {
        return 'text-blue-600 font-medium'; // 
      } else if (daysDiff < 0) {
        return 'text-red-600 font-medium'; // 
      } else {
        return 'text-green-600 font-medium'; // 
      }
    } catch (error) {
      return 'text-gray-500';
    }
  };
  if (loading) {
    return (
      <div className="bg-white rounded-lg shadow-sm border p-8 text-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-4"></div>
        <p className="text-gray-600">...</p>
      </div>
    );
  }

  if (data.length === 0) {
    return (
      <div className="bg-white rounded-lg shadow-sm border p-8 text-center">
        <Package className="w-12 h-12 text-gray-400 mx-auto mb-4" />
        <p className="text-gray-600"></p>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-sm border overflow-hidden mb-6">
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.drug_code')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.item_number')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.drug_name')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.manufacturer')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.arrival_time')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.received_expected_qty')}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                {t('table.detail_count')}
              </th>
              <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {data.map((group, index) => (
              <tr 
                key={`${group.CODE}-${group.SKDIACODE}`}
                className="hover:bg-gray-50 cursor-pointer transition-colors"
                onClick={() => onRowClick(group)}
              >
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="text-sm font-medium text-gray-900">
                    {group.CODE}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="text-sm text-gray-900">{group.SKDIACODE}</span>
                </td>
                <td className="px-6 py-4">
                  <div className="text-sm text-gray-900">
                    <div className="font-medium">
                      {group.CHT_NAME || group.NAME}
                    </div>
                    {group.CHT_NAME && group.NAME && group.CHT_NAME !== group.NAME && (
                      <div className="text-gray-500 text-xs mt-1">
                        {group.NAME}
                      </div>
                    )}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="text-sm text-gray-900">
                    {group.BRD || ''}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm space-y-1">
                    {group.items.map((item, itemIndex) => (
                      <div key={itemIndex} className={`flex items-center ${getArrivalTimeColor(item.DELIVERY_TIME)}`}>
                        <Calendar className="w-4 h-4 mr-1 text-gray-400" />
                        {formatArrivalTime(item.DELIVERY_TIME)}
                      </div>
                    ))}
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm text-gray-900">
                    <span className="font-semibold text-green-600">
                      {group.totalReceivedQty.toLocaleString()}
                    </span>
                    <span className="text-gray-400 mx-1">/</span>
                    <span className="font-semibold text-blue-600">
                      {group.totalStartQty.toLocaleString()}
                    </span>
                    <span className="text-gray-500 ml-1">
                      {group.PAKAGE}
                    </span>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                    {group.items.length} 
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right">
                  <ChevronRight className="w-5 h-5 text-gray-400" />
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};