import React, { useState, useEffect } from 'react';
import { Plus, X, Search, Package } from 'lucide-react';
import { getMedCloudData, createInspectionManually } from '../../services/inspection';
import { MedData } from '../../types/inspection';
import LoadingSpinner from '../common/LoadingSpinner';

interface ManualCreateModalProps {
  isOpen: boolean;
  onClose: () => void;
  onDataUpdate?: () => void;
}

export const ManualCreateModal: React.FC<ManualCreateModalProps> = ({
  isOpen,
  onClose,
  onDataUpdate,
}) => {
  const [medData, setMedData] = useState<MedData[]>([]);
  const [loading, setLoading] = useState(false);
  const [medDataLoading, setMedDataLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  
  const [formData, setFormData] = useState({
    pon: '',
    icSn: '',
    code: '',
    name: '',
    brd: '',
    skdiacode: '',
    startQty: ''
  });

  const [filteredMedData, setFilteredMedData] = useState<MedData[]>([]);
  const [showCodeSuggestions, setShowCodeSuggestions] = useState(false);
  const [showNameSuggestions, setShowNameSuggestions] = useState(false);

  // 
  const loadMedData = async () => {
    setMedDataLoading(true);
    try {
      const response = await getMedCloudData();
      if (response.Code === 200 && response.Data) {
        setMedData(response.Data);
        console.log(':', response.Data.length, '');
      } else {
        console.error(':', response.Result);
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setMedDataLoading(false);
    }
  };

  // 
  useEffect(() => {
    if (isOpen && medData.length === 0) {
      loadMedData();
    }
  }, [isOpen]);

  // 
  const resetForm = () => {
    setFormData({
      pon: '',
      icSn: '',
      code: '',
      name: '',
      brd: '',
      skdiacode: '',
      startQty: ''
    });
    setError(null);
    setSuccess(null);
    setShowCodeSuggestions(false);
    setShowNameSuggestions(false);
    setFilteredMedData([]);
  };

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleCodeChange = (value: string) => {
    setFormData(prev => ({ ...prev, code: value }));
    
    if (value.trim()) {
      const filtered = medData.filter(med => 
        med.CODE.toLowerCase().includes(value.toLowerCase())
      );
      setFilteredMedData(filtered);
      setShowCodeSuggestions(true);
      setShowNameSuggestions(false);
    } else {
      setShowCodeSuggestions(false);
      setFilteredMedData([]);
    }
  };

  // 
  const handleNameChange = (value: string) => {
    setFormData(prev => ({ ...prev, name: value }));
    
    if (value.trim()) {
      const filtered = medData.filter(med => 
        med.NAME.toLowerCase().includes(value.toLowerCase()) ||
        (med.CHT_NAME && med.CHT_NAME.toLowerCase().includes(value.toLowerCase())) ||
        med.DIANAME.toLowerCase().includes(value.toLowerCase()) ||
        med.TRADENAME.toLowerCase().includes(value.toLowerCase())
      );
      setFilteredMedData(filtered);
      setShowNameSuggestions(true);
      setShowCodeSuggestions(false);
    } else {
      setShowNameSuggestions(false);
      setFilteredMedData([]);
    }
  };

  // 
  const selectMed = (med: MedData) => {
    setFormData(prev => ({
      ...prev,
      code: med.CODE,
      name: med.NAME,
      skdiacode: med.SKDIACODE,
      brd: med.BRD || prev.brd
    }));
    setShowCodeSuggestions(false);
    setShowNameSuggestions(false);
    setFilteredMedData([]);
  };

  // 
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      const response = await createInspectionManually(
        formData.pon,
        formData.icSn,
        formData.code,
        formData.name,
        formData.brd,
        formData.skdiacode,
        formData.startQty
      );

      if (response.Code === 200) {
        setSuccess('');
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
        
        // 3
        setTimeout(() => {
          handleClose();
        }, 3000);
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between p-6 border-b">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <Plus className="w-5 h-5 mr-2" />
              
            </h3>
            <p className="text-sm text-gray-600 mt-1">
              
            </p>
          </div>
          <button
            onClick={handleClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-6">
          {medDataLoading && (
            <div className="mb-4 p-3 bg-blue-50 border border-blue-200 text-blue-700 rounded-lg text-sm flex items-center">
              <LoadingSpinner size="small" className="mr-2" />
              ...
            </div>
          )}

          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          {success && (
            <div className="mb-4 p-3 bg-green-50 border border-green-200 text-green-700 rounded-lg text-sm">
              {success}
            </div>
          )}

          <div className="space-y-4">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <input
                type="text"
                value={formData.pon}
                onChange={(e) => setFormData({ ...formData, pon: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                required
                placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                
              </label>
              <input
                type="text"
                value={formData.icSn}
                onChange={(e) => setFormData({ ...formData, icSn: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=""
              />
            </div>

            {/*  */}
            <div className="relative">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <div className="relative">
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => handleCodeChange(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  required
                  placeholder=""
                />
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <Search className="h-4 w-4 text-gray-400" />
                </div>
              </div>
              
              {/*  */}
              {showCodeSuggestions && filteredMedData.length > 0 && (
                <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-60 overflow-y-auto">
                  {filteredMedData.slice(0, 10).map((med, index) => (
                    <div
                      key={index}
                      onClick={() => selectMed(med)}
                      className="px-3 py-2 hover:bg-gray-100 cursor-pointer border-b border-gray-100 last:border-b-0"
                    >
                      <div className="font-medium text-sm">{med.CODE}</div>
                      <div className="text-xs text-gray-600">{med.CHT_NAME || med.NAME}</div>
                      <div className="text-xs text-gray-500">: {med.SKDIACODE}</div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/*  */}
            <div className="relative">
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <div className="relative">
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => handleNameChange(e.target.value)}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                  required
                  placeholder=""
                />
                <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                  <Search className="h-4 w-4 text-gray-400" />
                </div>
              </div>
              
              {/*  */}
              {showNameSuggestions && filteredMedData.length > 0 && (
                <div className="absolute z-10 w-full mt-1 bg-white border border-gray-300 rounded-md shadow-lg max-h-60 overflow-y-auto">
                  {filteredMedData.slice(0, 10).map((med, index) => (
                    <div
                      key={index}
                      onClick={() => selectMed(med)}
                      className="px-3 py-2 hover:bg-gray-100 cursor-pointer border-b border-gray-100 last:border-b-0"
                    >
                      <div className="font-medium text-sm">{med.NAME}</div>
                      <div className="text-xs text-gray-600">: {med.CODE}</div>
                      <div className="text-xs text-gray-500">: {med.SKDIACODE}</div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <input
                type="text"
                value={formData.skdiacode}
                onChange={(e) => setFormData({ ...formData, skdiacode: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                required
                placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                
              </label>
              <input
                type="text"
                value={formData.brd}
                onChange={(e) => setFormData({ ...formData, brd: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                placeholder=""
              />
            </div>

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                 *
              </label>
              <input
                type="number"
                value={formData.startQty}
                onChange={(e) => setFormData({ ...formData, startQty: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                required
                min="1"
                step="1"
                placeholder=""
              />
            </div>
          </div>

          <div className="flex gap-3 mt-6">
            <button
              type="button"
              onClick={handleClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              disabled={loading}
            >
              
            </button>
            <button
              type="submit"
              disabled={loading || medDataLoading}
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
            >
              {loading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Plus className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};