import React, { useState, useRef } from 'react';
import { Upload, X, FileSpreadsheet, AlertCircle, CheckCircle } from 'lucide-react';
import { uploadExcelFile } from '../../services/inspection';
import { getLoggedID, getLoggedName } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';

interface ExcelUploadModalProps {
  isOpen: boolean;
  onClose: () => void;
  onDataUpdate?: () => void;
}

export const ExcelUploadModal: React.FC<ExcelUploadModalProps> = ({
  isOpen,
  onClose,
  onDataUpdate,
}) => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // 
  const resetForm = () => {
    setSelectedFile(null);
    setError(null);
    setSuccess(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // 
    const allowedTypes = [
      'application/vnd.ms-excel',
      'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      '.xls',
      '.xlsx'
    ];
    
    const isExcelFile = allowedTypes.some(type => 
      file.type === type || file.name.toLowerCase().endsWith(type)
    );

    if (!isExcelFile) {
      setError(' Excel  (.xls  .xlsx)');
      return;
    }

    //  ( 50MB)
    if (file.size > 50 * 1024 * 1024) {
      setError(' 50MB');
      return;
    }

    setSelectedFile(file);
    setError(null);
    setSuccess(null);
  };

  // 
  const handleUpload = async () => {
    if (!selectedFile) return;

    const opId = getLoggedID();
    const opName = getLoggedName();

    if (!opId || !opName) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);
    setSuccess(null);

    try {
      console.log('=== Excel Upload Debug ===');
      console.log('Selected file:', selectedFile.name, selectedFile.size, 'bytes');
      console.log('Operator ID:', opId);
      console.log('Operator Name:', opName);

      const response = await uploadExcelFile(selectedFile, opId, opName);

      console.log('Response Data:', response);
      console.log('===============================');

      if (response.Code === 200) {
        console.log(' Excel upload successful');
        setSuccess(response.Result || 'Excel ');
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
        
        // 3
        setTimeout(() => {
          handleClose();
        }, 3000);
      } else {
        console.log(' Excel upload failed');
        setError(response.Result || '');
      }
    } catch (err) {
      console.error('Excel upload error:', err);
      console.log(' Exception occurred during upload');
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between p-6 border-b">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <Upload className="w-5 h-5 mr-2" />
              
            </h3>
            <p className="text-sm text-gray-600 mt-1">
               Excel 
            </p>
          </div>
          <button
            onClick={handleClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <div className="p-6">
          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
              <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
              {error}
            </div>
          )}

          {success && (
            <div className="mb-4 p-3 bg-green-50 border border-green-200 text-green-700 rounded-lg text-sm flex items-center">
              <CheckCircle className="w-4 h-4 mr-2 flex-shrink-0" />
              {success}
            </div>
          )}

          <div className="space-y-4">
            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 Excel 
              </label>
              <div
                className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-gray-400 transition-colors cursor-pointer"
                onClick={() => fileInputRef.current?.click()}
              >
                <input
                  ref={fileInputRef}
                  type="file"
                  accept=".xls,.xlsx,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                  onChange={handleFileSelect}
                  className="hidden"
                />
                
                {selectedFile ? (
                  <div className="space-y-2">
                    <FileSpreadsheet className="w-12 h-12 text-green-500 mx-auto" />
                    <div>
                      <p className="text-sm font-medium text-gray-900">
                        {selectedFile.name}
                      </p>
                      <p className="text-xs text-gray-500">
                        {(selectedFile.size / 1024 / 1024).toFixed(2)} MB
                      </p>
                      <p className="text-xs text-gray-500 mt-1">
                        
                      </p>
                    </div>
                  </div>
                ) : (
                  <div className="space-y-2">
                    <FileSpreadsheet className="w-12 h-12 text-gray-400 mx-auto" />
                    <div>
                      <p className="text-sm text-gray-600"> Excel </p>
                      <p className="text-xs text-gray-500"> .xls  .xlsx  50MB</p>
                    </div>
                  </div>
                )}
              </div>
            </div>

            {/*  */}
            <div className="bg-blue-50 rounded-lg p-4">
              <h4 className="text-sm font-medium text-blue-800 mb-2"></h4>
              <ul className="text-xs text-blue-700 space-y-1">
                <li>  Excel </li>
                <li>  50MB</li>
                <li> </li>
                <li> </li>
              </ul>
            </div>
          </div>

          <div className="flex gap-3 mt-6">
            <button
              type="button"
              onClick={handleClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              disabled={loading}
            >
              
            </button>
            <button
              onClick={handleUpload}
              disabled={loading || !selectedFile}
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
            >
              {loading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Upload className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};