import React, { useState, useEffect } from 'react';
import { X, Package, CheckCircle, Send, AlertCircle, ZoomIn, Link, CreditCard as Edit, Save, XCircle } from 'lucide-react';
import LoadingSpinner from '../common/LoadingSpinner';
import { PairingModal } from './PairingModal';
import { getTextVisionByGuid, updateTextVisionData } from '../../services/inspection';
import { addSubContent } from '../../services/inspection';
import { getLoggedName } from '../../services/auth';
import { apiCall } from '../../utils/api';

interface ImageZoomModalProps {
  isOpen: boolean;
  onClose: () => void;
  imageUrl: string;
  title: string;
}

const ImageZoomModal: React.FC<ImageZoomModalProps> = ({ isOpen, onClose, imageUrl, title }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center p-4 z-[60]">
      <div className="relative max-w-[90vw] max-h-[90vh]">
        <button
          onClick={onClose}
          className="absolute -top-10 right-0 text-white hover:text-gray-300 transition-colors"
        >
          <X className="w-8 h-8" />
        </button>
        <img
          src={imageUrl}
          alt={title}
          className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
        />
        <div className="absolute bottom-0 left-0 right-0 bg-black bg-opacity-50 text-white p-2 rounded-b-lg">
          <p className="text-center text-sm">{title}</p>
        </div>
      </div>
    </div>
  );
};

interface BatchAnalysisResultModalProps {
  isOpen: boolean;
  onClose: () => void;
  results: any[];
  onDataUpdate?: () => void;
}

export const BatchAnalysisResultModal: React.FC<BatchAnalysisResultModalProps> = ({
  isOpen,
  onClose,
  results,
  onDataUpdate,
}) => {
  const [currentResults, setCurrentResults] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [zoomImage, setZoomImage] = useState<{ url: string; title: string } | null>(null);
  const [pairingModal, setPairingModal] = useState<{ isOpen: boolean; result: any }>({
    isOpen: false,
    result: null
  });
  const [pairedGuids, setPairedGuids] = useState<Set<string>>(new Set());
  const [editingGuid, setEditingGuid] = useState<string | null>(null);
  const [editForm, setEditForm] = useState({
    po_num: '',
    qty: '',
    batch_num: '',
    expirydate: ''
  });
  const [updateLoading, setUpdateLoading] = useState<string | null>(null);
  const [batchSubmitLoading, setBatchSubmitLoading] = useState(false);
  const [batchSubmitError, setBatchSubmitError] = useState<string | null>(null);
  const [submittedGuids, setSubmittedGuids] = useState<Set<string>>(new Set());

  //  results prop 
  useEffect(() => {
    if (results && results.length > 0 && currentResults.length === 0) {
      setCurrentResults(results);
    }
  }, [results, currentResults.length]);

  //  API 
  const refreshResultByGuid = async (guid: string) => {
    try {
      console.log('===  ===');
      console.log('GUID:', guid);
      
      const response = await getTextVisionByGuid(guid);
      
      console.log('API Response:', response);
      
      if (response.Code === 200 && response.Data && response.Data.length > 0) {
        const latestData = response.Data[0];
        console.log(':', latestData);
        
        // 
        setCurrentResults(prev => {
          const idx = prev.findIndex(r => r.GUID === guid);
          if (idx < 0) {
            console.warn(' GUID:', guid);
            return prev;
          }
          
          const next = [...prev];
          next[idx] = {
            ...prev[idx],        // 
            ...latestData,       // 
            base64: prev[idx].base64 // 
          };
          
          console.log(' :', idx);
          console.log(':', next[idx]);
          return next;
        });
        
      } else {
        console.error(':', response.Result);
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    }
  };

  // 
  const handleImageClick = (result: any, index: number) => {
    console.log('Image clicked:', { index, hasBase64: !!result.base64 });
    
    // 
    const imageData = result.base64 || result.image || result.img || result.picture;
    if (!imageData) {
      console.warn('No image data found for result:', result);
      return;
    }
    
    const imageUrl = imageData.startsWith('data:') 
      ? imageData 
      : `data:image/jpeg;base64,${imageData}`;
    
    console.log('Setting zoom image:', { url: imageUrl.substring(0, 50) + '...', title: ` #${index + 1}` });
    
    setZoomImage({
      url: imageUrl,
      title: ` #${index + 1}`
    });
  };

  // 
  const handlePairingClick = (result: any) => {
    console.log(':', result);
    
    setPairingModal({
      isOpen: true,
      result: result
    });
  };

  // 
  const handleEditClick = (result: any) => {
    setEditingGuid(result.GUID);
    setEditForm({
      po_num: result.po_num || '',
      qty: result.qty || '',
      batch_num: result.batch_num || '',
      expirydate: result.expirydate ? result.expirydate.split(' ')[0] : '' // 
    });
  };

  // 
  const handleEditCancel = () => {
    setEditingGuid(null);
    setEditForm({
      po_num: '',
      qty: '',
      batch_num: '',
      expirydate: ''
    });
  };

  // 
  const handleEditSave = async (result: any) => {
    setUpdateLoading(result.GUID);
    setError(null);

    try {
      console.log('===  ===');
      console.log('GUID:', result.GUID);
      console.log(':', editForm);

      // 
      const formattedExpiryDate = editForm.expirydate ? `${editForm.expirydate} 00:00:00` : '';

      const response = await updateTextVisionData({
        GUID: result.GUID,
        po_num: editForm.po_num,
        qty: editForm.qty,
        batch_num: editForm.batch_num,
        expirydate: formattedExpiryDate
      });

      console.log(' API :', response);

      if (response.Code === 200) {
        console.log(' ');
        // 
        await refreshResultByGuid(result.GUID);
        // 
        setEditingGuid(null);
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setUpdateLoading(null);
    }
  };

  // 
  const canPair = (result: any) => {
    // 
    if (pairedGuids.has(result.GUID)) {
      return { canPair: false, reason: '' };
    }
    
    // ""
    if (result.check === '') {
      return { canPair: false, reason: '' };
    }
    
    return { canPair: true, reason: '' };
  };
  // 
  const handlePairingSuccess = (resultGuid: string) => {
    console.log('GUID:', resultGuid);
    
    //  API 
    refreshResultByGuid(resultGuid);
    
    // 
    setPairingModal({ isOpen: false, result: null });
  };

  // 
  const canSubmit = (result: any): boolean => {
    // 
    if (submittedGuids.has(result.GUID)) return false;
    return !!(result.po_num && result.qty && result.batch_num && result.expirydate);
  };

  // 
  const handleBatchConfirmSubmit = async () => {
  // 
  const submittableResults = currentResults.filter(
    (result) => canSubmit(result) && !submittedGuids.has(result.GUID)
  );

  if (submittableResults.length === 0) {
    setBatchSubmitError('');
    return;
  }

  const loggedName = getLoggedName();
  if (!loggedName) {
    setBatchSubmitError('');
    return;
  }

  setBatchSubmitLoading(true);
  setBatchSubmitError(null);

  try {
    console.log(
      ` GUID  ${submittableResults.length} :`,
      submittableResults.map(result => result.GUID)
    );

    // check
    const guids = submittableResults.map(result => result.GUID).join(';');
    const checkRequest = { ValueAry: [null, guids] }; // 

    const checkResponse = await apiCall('/api/pcmpo/check', {
      method: 'POST',
      body: checkRequest,
    });

    if (checkResponse.Code !== 200) {
      setBatchSubmitError(checkResponse.Result || '');
      return;
    }

    //  GUID
    const subContentData = submittableResults.map(result => ({
      GUID: result.GUID, // <== 
      Master_GUID: result.Master_GUID,
      LOT: result.batch_num,
      END_QTY: result.qty,
      VAL: result.expirydate ? `${result.expirydate.split(' ')[0]} 00:00:00` : '',
      OP: loggedName
    }));

    const successGuids: string[] = [];
    const errors: string[] = [];

    for (const result of subContentData) {
      try {
        const addResponse = await addSubContent(
          result.Master_GUID,
          result.LOT,
          result.END_QTY,
          result.VAL,
          result.OP
        );

        if (addResponse.Code === 200) {
          successGuids.push(result.GUID); //  GUID
          console.log(` : ${result.Master_GUID}`);
        } else {
          errors.push(`${result.Master_GUID}: ${addResponse.Result}`);
          console.log(` : ${result.Master_GUID} - ${addResponse.Result}`);
        }
      } catch (err) {
        const msg = err instanceof Error ? err.message : '';
        errors.push(`${result.Master_GUID}: ${msg}`);
        console.error(` : ${result.Master_GUID}`, err);
      }
    }

    //   GUID
    if (successGuids.length > 0) {
      setSubmittedGuids(prev => new Set([...prev, ...successGuids]));
      ///
      setCurrentResults(prev => prev.map(result =>
        successGuids.includes(result.GUID) ? { ...result, _locked: true } : result
      ));
    }

    // UI 
    if (errors.length === 0) {
      setBatchSubmitError(null);
      if (onDataUpdate) onDataUpdate();
    } else if (successGuids.length > 0) {
      setBatchSubmitError(`${successGuids.length} ${errors.length} ${errors.join('; ')}`);
      if (onDataUpdate) onDataUpdate();
    } else {
      setBatchSubmitError(`${errors.join('; ')}`);
    }
  } catch (err) {
    console.error(':', err);
    setBatchSubmitError(err instanceof Error ? err.message : '');
  } finally {
    setBatchSubmitLoading(false);
  }
};


  // 
  const handleClose = async () => {
    // 
    const unsubmittedResults = currentResults.filter(result => 
      !submittedGuids.has(result.GUID)
    );

    if (unsubmittedResults.length > 0) {
      try {
        console.log('===  ===');
        console.log(':', unsubmittedResults.length);
        console.log(' GUIDs:', unsubmittedResults.map(r => r.GUID));

        const guidsToDelete = unsubmittedResults.map(result => result.GUID).join(';');
        const deleteRequest = {
          ValueAry: [guidsToDelete]
        };

        console.log('=== Delete API Request Debug ===');
        console.log('URL:', '/api/pcmpo/delete_by_GUID');
        console.log('Method:', 'POST');
        console.log('Request Data:', JSON.stringify(deleteRequest, null, 2));

        const deleteResponse = await apiCall('/api/pcmpo/delete_by_GUID', {
          method: 'POST',
          body: deleteRequest,
        });

        console.log('Delete API Response:', JSON.stringify(deleteResponse, null, 2));

        if (deleteResponse.Code === 200) {
          console.log(' ');
        } else {
          console.log(' :', deleteResponse.Result);
        }
      } catch (err) {
        console.error(':', err);
      }
    }

    // 
    if (submittedGuids.size > 0 && onDataUpdate) {
      onDataUpdate();
    }
    onClose();
  };

  if (!isOpen || !results || results.length === 0) return null;

  // 
  const formatExpiryDate = (dateStr: string): string => {
    if (!dateStr) return '';
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('zh-TW', { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit' 
      }).replace(/\//g, '/');
    } catch {
      return dateStr;
    }
  };

  return (
    <>
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
        <div className="bg-white rounded-lg shadow-xl max-w-6xl w-full max-h-[90vh] overflow-hidden">
          <div className="flex items-center justify-between p-6 border-b bg-gray-50">
            <div>
              <h3 className="text-lg font-semibold text-gray-900">
                
              </h3>
              <div className="flex items-center mt-1">
                <CheckCircle className="w-4 h-4 text-green-500 mr-1" />
                <span className="text-sm text-gray-600"> {currentResults.length} </span>
              </div>
            </div>
            <button
              onClick={handleClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <X className="w-6 h-6" />
            </button>
          </div>

          <div className="p-6 overflow-y-auto max-h-[calc(90vh-200px)]">
            {error && (
              <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
                <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
                {error}
              </div>
            )}

            <div className="grid grid-cols-1 lg:grid-cols-2 xl:grid-cols-3 gap-6">
              {currentResults.map((result, index) => (
                <div key={index} className={`border rounded-lg p-4 ${
                  submittedGuids.has(result.GUID) 
                    ? 'bg-blue-50 border-blue-200' 
                    : 'bg-gray-50'
                }`}>
                  {/*  */}
                  <div className="mb-2">
                    {canSubmit(result) ? (
                      result.Result === '' || result.check === '' || result._isFailed ? (
                        <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                          <CheckCircle className="w-3 h-3 mr-1" />
                          {result._isFailed ? '' : ''}
                        </span>
                      ) : (
                        <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-green-100 text-green-800">
                          <CheckCircle className="w-3 h-3 mr-1" />
                          
                        </span>
                      )
                    ) : (
                      <>
                        {result.Result === '' || result.check === '' ? (
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                            <AlertCircle className="w-3 h-3 mr-1" />
                            
                          </span>
                        ) : (
                          <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                            <AlertCircle className="w-3 h-3 mr-1" />
                            
                          </span>
                        )}
                      </>
                    )}
                  </div>

                  <div className="mb-4">
                    <div className="flex items-center justify-between mb-2">
                      <h4 className="text-md font-medium text-gray-900">
                         #{index + 1}
                      </h4>
                      <div className="flex gap-2">
                        {editingGuid === result.GUID ? (
                          <>
                            <button
                              onClick={() => handleEditSave(result)}
                              disabled={updateLoading === result.GUID}
                              className="px-2 py-1 bg-green-600 text-white text-xs rounded hover:bg-green-700 transition-colors flex items-center"
                            >
                              {updateLoading === result.GUID ? (
                                <LoadingSpinner size="small" className="mr-1" />
                              ) : (
                                <Save className="w-3 h-3 mr-1" />
                              )}
                              
                            </button>
                            <button
                              onClick={handleEditCancel}
                              disabled={updateLoading === result.GUID}
                              className="px-2 py-1 bg-gray-500 text-white text-xs rounded hover:bg-gray-600 transition-colors flex items-center"
                            >
                              <XCircle className="w-3 h-3 mr-1" />
                              
                            </button>
                          </>
                        ) : (
                          <>
                            <button
                              onClick={() => handleEditClick(result)}
                              className="px-2 py-1 bg-orange-600 text-white text-xs rounded hover:bg-orange-700 transition-colors flex items-center"
                            >
                              <Edit className="w-3 h-3 mr-1" />
                              
                            </button>
                            {(() => {
                              const pairStatus = canPair(result);
                              return (
                                <button
                                  onClick={() => handlePairingClick(result)}
                                  className={`px-2 py-1 text-xs rounded transition-colors flex items-center ${
                                    pairStatus.canPair
                                      ? 'bg-blue-600 text-white hover:bg-blue-700'
                                      : 'bg-gray-400 text-white cursor-not-allowed'
                                  }`}
                                  disabled={!pairStatus.canPair}
                                  title={!pairStatus.canPair ? pairStatus.reason : ''}
                                >
                                  <Link className="w-3 h-3 mr-1" />
                                  {pairStatus.canPair ? '' : pairStatus.reason}
                                </button>
                              );
                            })()}
                          </>
                        )}
                      </div>
                    </div>
                    
                    {/*  */}
                    {result.base64 && (
                      <div className="mb-4">
                        <div 
                          className="relative group cursor-pointer"
                          onClick={() => handleImageClick(result, index)}
                        >
                          <img
                            src={result.base64.startsWith('data:') ? result.base64 : `data:image/jpeg;base64,${result.base64}`}
                            alt={` ${index + 1}`}
                            className="w-full h-32 object-cover rounded border hover:opacity-80 transition-opacity"
                          />
                          <div className="absolute inset-0 flex items-center justify-center opacity-0 group-hover:opacity-100 transition-opacity bg-black bg-opacity-30 rounded pointer-events-none">
                            <ZoomIn className="w-8 h-8 text-white" />
                          </div>
                        </div>
                      </div>
                    )}
                  </div>

                  {/*  */}
                  <div className="space-y-3">
                    {/*  */}
                    <div className="bg-blue-50 rounded-lg p-3">
                      <h5 className="text-sm font-medium text-blue-800 mb-2 flex items-center">
                        <Package className="w-4 h-4 mr-1" />
                        
                      </h5>
                      <div className="space-y-1 text-sm">
                        <div>
                          <span className="text-blue-600">:</span>
                          <span className="font-medium text-blue-900 ml-1">{result.name}</span>
                        </div>
                        {result.cht_name && (
                          <div>
                            <span className="text-blue-600">:</span>
                            <span className="font-medium text-blue-900 ml-1">{result.cht_name}</span>
                          </div>
                        )}
                        <div>
                          <span className="text-blue-600">:</span>
                          <span className="font-medium text-blue-900 ml-1">{result.code}</span>
                        </div>
                      </div>
                    </div>

                    {/*  */}
                    <div className="bg-green-50 rounded-lg p-3 border-2 border-green-200">
                      <h5 className="text-sm font-medium text-green-800 mb-2"></h5>
                      {editingGuid === result.GUID ? (
                        <input
                          type="text"
                          value={editForm.po_num}
                          onChange={(e) => setEditForm(prev => ({ ...prev, po_num: e.target.value }))}
                          className="w-full px-2 py-1 text-center text-lg font-bold border border-green-300 rounded focus:ring-2 focus:ring-green-500 focus:border-green-500"
                          placeholder=""
                        />
                      ) : (
                        <div className="text-center">
                          <div className="text-2xl font-bold text-green-700">
                            {result.po_num}
                          </div>
                        </div>
                      )}
                    </div>

                    {/*  -  */}
                    {result.qty && (
                      <div className="bg-orange-50 rounded-lg p-3 border-2 border-orange-200">
                        <h5 className="text-sm font-medium text-orange-800 mb-2"></h5>
                        {editingGuid === result.GUID ? (
                          <input
                            type="number"
                            value={editForm.qty}
                            onChange={(e) => setEditForm(prev => ({ ...prev, qty: e.target.value }))}
                            className="w-full px-2 py-1 text-center text-2xl font-bold border border-orange-300 rounded focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                            placeholder=""
                          />
                        ) : (
                          <div className="text-center">
                            <div className="text-3xl font-bold text-orange-700">
                              {result.qty}
                            </div>
                          </div>
                        )}
                      </div>
                    )}

                    {/*  */}
                    <div className="bg-yellow-50 rounded-lg p-3">
                      <h5 className="text-sm font-medium text-yellow-800 mb-2"></h5>
                      {result._isFailed ? (
                        <div className="text-xs text-red-600 bg-red-50 px-2 py-1 rounded">
                          : {result._failureReason}
                        </div>
                      ) : editingGuid === result.GUID ? (
                        <input
                          type="text"
                          value={editForm.batch_num}
                          onChange={(e) => setEditForm(prev => ({ ...prev, batch_num: e.target.value }))}
                          className="w-full px-2 py-1 text-center text-lg font-bold border border-yellow-300 rounded focus:ring-2 focus:ring-yellow-500 focus:border-yellow-500"
                          placeholder=""
                        />
                      ) : (
                        <div className="text-center">
                          <div className="text-lg font-bold text-yellow-900">
                            {result.batch_num}
                          </div>
                        </div>
                      )}
                    </div>

                    {/*  */}
                    <div className="bg-purple-50 rounded-lg p-3">
                      <h5 className="text-sm font-medium text-purple-800 mb-2"></h5>
                      {editingGuid === result.GUID ? (
                        <input
                          type="date"
                          value={editForm.expirydate}
                          onChange={(e) => setEditForm(prev => ({ ...prev, expirydate: e.target.value }))}
                          className="w-full px-2 py-1 text-center text-lg font-bold border border-purple-300 rounded focus:ring-2 focus:ring-purple-500 focus:border-purple-500"
                        />
                      ) : (
                        <div className="text-center">
                          <div className="text-lg font-bold text-purple-900">
                            {formatExpiryDate(result.expirydate)}
                          </div>
                        </div>
                      )}
                    </div>

                    {/*  */}
                    <div className="bg-gray-100 rounded-lg p-3">
                      <h5 className="text-sm font-medium text-gray-800 mb-2"></h5>
                      <div className="space-y-1 text-xs">
                        <div className="flex justify-between">
                          <span className="text-gray-600">:</span>
                          <span className="font-medium text-gray-900">{result.IC_SN}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">:</span>
                          <span className="font-medium text-gray-900">{result.op_name}</span>
                        </div>
                        <div className="flex justify-between">
                          <span className="text-gray-600">:</span>
                          <span className="font-medium text-gray-900">{result.op_time}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>

          {batchSubmitError && (
            <div className="mx-6 mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
              <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
              {batchSubmitError}
            </div>
          )}

          <div className="flex justify-end p-6 border-t bg-gray-50">
            <div className="text-sm text-gray-600 flex items-center">
              <div className="mr-4">
                : {currentResults.filter(result => canSubmit(result) && !submittedGuids.has(result.GUID)).length}  | 
                : {submittedGuids.size}  | 
                : {currentResults.filter(result => !submittedGuids.has(result.GUID)).length} 
              </div>
            </div>
            
            <div className="flex gap-3">
              <button
                onClick={handleBatchConfirmSubmit}
                disabled={batchSubmitLoading || currentResults.filter(result => canSubmit(result) && !submittedGuids.has(result.GUID)).length === 0}
                className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center"
              >
                {batchSubmitLoading ? (
                  <>
                    <LoadingSpinner size="small" className="mr-2" />
                    ...
                  </>
                ) : (
                  <>
                    <Send className="w-4 h-4 mr-2" />
                     ({currentResults.filter(result => canSubmit(result) && !submittedGuids.has(result.GUID)).length})
                  </>
                )}
              </button>
              <button
                onClick={handleClose}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
                disabled={batchSubmitLoading}
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <ImageZoomModal
        isOpen={!!zoomImage}
        onClose={() => setZoomImage(null)}
        imageUrl={zoomImage?.url || ''}
        title={zoomImage?.title || ''}
      />

      {/*  */}
      <PairingModal
        isOpen={pairingModal.isOpen}
        onClose={() => setPairingModal({ isOpen: false, result: null })}
        analysisResult={pairingModal.result}
        onSuccess={(resultGuid) => handlePairingSuccess(resultGuid)}
      />
    </>
  );
};