import React, { useState, useEffect, useCallback } from 'react';
import { AlertCircle, Package, TrendingUp, Layers, Search, Plus, Globe, Camera } from 'lucide-react';
import { InspectionContent, GroupedInspection } from '../types/inspection';
import { getInspectionsByDateRange, getDefaultDateRange, groupInspectionsByDrug, getcontentBysubcontentAddtime, searchByBarcode } from '../services/inspection';
import { getLoggedName, getLoggedEmployer } from '../services/auth';
import { useLanguage } from '../contexts/LanguageContext';
import { InspectionTable } from '../components/inspection/InspectionTable';
import { InspectionDetail } from '../components/inspection/InspectionDetail';
import { DateRangeFilter } from '../components/inspection/DateRangeFilter';
import { BatchUploadModal } from '../components/inspection/BatchUploadModal';
import { InspectionListModal } from '../components/inspection/InspectionListModal';
import { ExcelUploadModal } from '../components/inspection/ExcelUploadModal';
import { ManualCreateModal } from '../components/inspection/ManualCreateModal';
import { BarcodeScannerModal } from '../components/inspection/BarcodeScannerModal';
import { CreateBarcodeModal } from '../components/inspection/CreateBarcodeModal';
import { getConfig } from '../config';
import { useWebSocket } from '../hooks/useWebSocket';

export const InspectionPage: React.FC = () => {
  const { t, language, toggleLanguage } = useLanguage();
  const [inspections, setInspections] = useState<InspectionContent[]>([]);
  const [groupedInspections, setGroupedInspections] = useState<GroupedInspection[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedDetail, setSelectedDetail] = useState<GroupedInspection | null>(null);
  const [batchUploadOpen, setBatchUploadOpen] = useState(false);
  const [excelUploadOpen, setExcelUploadOpen] = useState(false);
  const [manualCreateOpen, setManualCreateOpen] = useState(false);
  const [inspectionListOpen, setInspectionListOpen] = useState(false);
  const [todayInspectionListOpen, setTodayInspectionListOpen] = useState(false);
  const [todayInspections, setTodayInspections] = useState<InspectionContent[]>([]);
  const [todayInspectionCount, setTodayInspectionCount] = useState(0);
  const [customTodayInspections, setCustomTodayInspections] = useState<InspectionContent[]>([]);
  const [searchBarcode, setSearchBarcode] = useState('');
  const [searchLoading, setSearchLoading] = useState(false);
  const [filteredInspections, setFilteredInspections] = useState<GroupedInspection[]>([]);
  const [isSearchActive, setIsSearchActive] = useState(false);
  const [searchNumber, setSearchNumber] = useState('');
  const [numberSearchLoading, setNumberSearchLoading] = useState(false);
  const [isNumberSearchActive, setIsNumberSearchActive] = useState(false);
  const [showLockedData, setShowLockedData] = useState(false);
  const [barcodeScannerOpen, setBarcodeScannerOpen] = useState(false);
  const [createBarcodeOpen, setCreateBarcodeOpen] = useState(false);
  const [failedBarcode, setFailedBarcode] = useState('');
  const handleIconClick = () => {
    const config = getConfig();
    if (config?.domain) {
      window.location.href = config.domain;
    }
  };
  // 
  const [dateRange, setDateRange] = useState<[string, string]>(getDefaultDateRange());
  const [startDate, endDate] = dateRange;

  //  WebSocket
  useWebSocket({
    onMessage: (message: any) => {
      console.log('=== Received WebSocket Message ===');
      console.log('Message:', message);

      // 
      loadInspections();
      loadTodayInspectionCount();
    },
    autoConnect: true,
  });

  // 
  const loadInspections = async (start?: string, end?: string) => {
    setLoading(true);
    setError(null);
    
    try {
      const [startDateTime, endDateTime] = start && end ? [start, end] : dateRange;
      const response = await getInspectionsByDateRange(startDateTime, endDateTime);
      console.log('Response Data:', response);
      if (response.Code === 200) {
        setInspections(response.Data);
        const grouped = groupInspectionsByDrug(response.Data, showLockedData);
        setGroupedInspections(grouped);
        
        // 
        if (selectedDetail) {
          const updatedDetail = grouped.find(
            group => group.CODE === selectedDetail.CODE && 
                    group.SKDIACODE === selectedDetail.SKDIACODE
          );
          if (updatedDetail) {
            setSelectedDetail(updatedDetail);
          }
        }
      } else {
        setError(response.Result || '');
        setInspections([]);
        setGroupedInspections([]);
      }
    } catch (err) {
      console.error('Failed to load inspections:', err);
      setError(err instanceof Error ? err.message : '');
      setInspections([]);
      setGroupedInspections([]);
    } finally {
      setLoading(false);
    }
  };

  // 
  const loadTodayInspectionCount = async () => {
    try {
      // 
      const taiwanTime = new Date();
      //  toLocaleString 
      const taiwanDateStr = taiwanTime.toLocaleDateString('zh-TW', {
        timeZone: 'Asia/Taipei',
        year: 'numeric',
        month: '2-digit',
        day: '2-digit'
      });
      
      //  (: YYYY/MM/DD)
      const [year, month, day] = taiwanDateStr.split('/');
      
      const startDate = `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')} 00:00:00`;
      const endDate = `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')} 23:59:59`;
      const response = await getcontentBysubcontentAddtime(startDate, endDate);
      if (response.Code === 200) {
        setTodayInspectionCount(response.Data.length);
        setTodayInspections(response.Data);
        setCustomTodayInspections(response.Data); // 
      } else {
        if (response.Result && response.Result.includes('')) {
          console.warn('No inspection data found for today:', response.Result);
        } else {
          console.error('Failed to load today inspection count:', response.Result);
        }
        setTodayInspectionCount(0);
        setTodayInspections([]);
        setCustomTodayInspections([]);
      }
    } catch (err) {
      console.error('Failed to load today inspection count:', err);
      setTodayInspectionCount(0);
      setTodayInspections([]);
      setCustomTodayInspections([]);
    }
  };

  // 
  const handleCustomDateRangeQuery = async (startDate: string, endDate: string) => {
    try {
      console.log('=== Custom Date Range Query ===');
      console.log('Start Date:', startDate);
      console.log('End Date:', endDate);
      
      const response = await getcontentBysubcontentAddtime(startDate, endDate);
      
      if (response.Code === 200) {
        setCustomTodayInspections(response.Data);
        console.log('Custom query result:', response.Data.length, '');
      } else {
        console.error('Custom date range query failed:', response.Result);
        setCustomTodayInspections([]);
      }
    } catch (err) {
      console.error('Custom date range query error:', err);
      setCustomTodayInspections([]);
    }
  };

  // 
  const handleBarcodeSearch = async (barcode: string) => {
    if (!barcode.trim()) {
      // 
      if (!isNumberSearchActive) {
        setFilteredInspections(groupedInspections);
      }
      setIsSearchActive(false);
      return;
    }
    

    setSearchLoading(true);
    setIsSearchActive(true);

    try {
      console.log('===  ===');
      console.log(':', barcode);

      // 
      if (loading || groupedInspections.length === 0) {
        console.log(' ...');
        setSearchLoading(false);
        return;
      }

      const response = await searchByBarcode(barcode);
      
      console.log(':', response);

      if (response.Code === 200 && response.Data && response.Data.length > 0) {
        console.log('API :', response.Data);
        console.log('API :', response.Data[0]);

        // 
        const searchedCodes = response.Data.map((item: any) => item.CODE).filter(Boolean);
        console.log(':', searchedCodes);

        // Debug:  groupedInspections  CODE
        console.log(' CODE:', groupedInspections.map(g => g.CODE));

        //
         const filtered = groupedInspections.filter(group => {
           const isMatch = searchedCodes.includes(group.CODE);
           if (isMatch) {
             console.log(' :', group.CODE);
           }
           return isMatch;
         });

        console.log(':', filtered.length, '');
        console.log(':', filtered);
        setFilteredInspections(filtered);
      } else if (response.Code === -200 && response.Result === '') {
        console.log('');
        setFailedBarcode(barcode);
        setCreateBarcodeOpen(true);
        setFilteredInspections([]);
      } else {
        console.log('');
        setFilteredInspections([]);
      }
    } catch (err) {
      console.error(':', err);
      setFilteredInspections([]);
    } finally {
      setSearchLoading(false);
    }
  };

  // /
  const handleNumberSearch = (searchValue: string) => {
    if (!searchValue.trim()) {
      // 
      if (!isSearchActive) {
        setFilteredInspections(groupedInspections);
      } else if (searchBarcode.trim()) {
        handleBarcodeSearch(searchBarcode);
        return;
      }
      setIsNumberSearchActive(false);
      return;
    }

    setNumberSearchLoading(true);
    setIsNumberSearchActive(true);

    try {
      console.log('===  ===');
      console.log(':', searchValue);

      // 
      const filtered = groupedInspections.filter(group => {
        // 
        return group.items.some(item => 
          item.IC_SN?.toLowerCase().includes(searchValue.toLowerCase()) ||
          item.PON?.toLowerCase().includes(searchValue.toLowerCase())
        );
      });

      console.log(':', filtered.length, '');
      setFilteredInspections(filtered);
    } catch (err) {
      console.error(':', err);
      setFilteredInspections([]);
    } finally {
      setNumberSearchLoading(false);
    }
  };

  // 
  const handleSearchInputChange = (value: string) => {
    setSearchBarcode(value);
  };

  //  Enter 
  const handleBarcodeKeyPress = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleBarcodeSearch(searchBarcode);
    }
  };

  // 
  const handleNumberSearchInputChange = (value: string) => {
    setSearchNumber(value);

    //  debounce 
    const timeoutId = setTimeout(() => {
      handleNumberSearch(value);
    }, 300);

    return () => clearTimeout(timeoutId);
  };

  // 
  const handleBarcodeScanned = useCallback((barcode: string) => {
    console.log('===  ===');
    console.log(':', barcode);

    // 
    setSearchBarcode(barcode);

    // 
    handleBarcodeSearch(barcode);
  }, []);

  // 
  useEffect(() => {
    loadInspections();
    loadTodayInspectionCount();
  }, []);

  //  groupedInspections 
  useEffect(() => {
    if (!isSearchActive && !isNumberSearchActive) {
      setFilteredInspections(groupedInspections);
    } else if (isSearchActive && searchBarcode.trim() && groupedInspections.length > 0) {
      //  handleBarcodeSearch 
      searchByBarcode(searchBarcode).then(response => {
        if (response.Code === 200 && response.Data && response.Data.length > 0) {
          const searchedCodes = response.Data.map((item: any) => item.CODE).filter(Boolean);
          const filtered = groupedInspections.filter(group => searchedCodes.includes(group.CODE));
          setFilteredInspections(filtered);
        } else {
          setFilteredInspections([]);
        }
      });
    } else if (isNumberSearchActive && searchNumber.trim() && groupedInspections.length > 0) {
      // 
      const filtered = groupedInspections.filter(group =>
        group.CODE.includes(searchNumber) ||
        group.NAME_CH?.includes(searchNumber) ||
        group.NAME_E?.includes(searchNumber) ||
        group.KSKLOT?.some((item: any) => item.KS_num?.includes(searchNumber))
      );
      setFilteredInspections(filtered);
    }
  }, [groupedInspections, isSearchActive, isNumberSearchActive, searchBarcode, searchNumber]);

  // 
  useEffect(() => {
    loadInspections();
  }, [showLockedData]);

  // 
  const handleDateChange = (newStartDate: string, newEndDate: string) => {
    setDateRange([newStartDate, newEndDate]);
  };

  // 
  const handleSearch = () => {
    loadInspections(startDate, endDate);
  };

  // 
  const handleReset = () => {
    const defaultRange = getDefaultDateRange();
    setDateRange(defaultRange);
    loadInspections(defaultRange[0], defaultRange[1]);
  };

  // 
  const totalInspections = inspections.length;
  const totalDrugs = isSearchActive ? filteredInspections.length : groupedInspections.length;
  
  // 
  const loggedName = getLoggedName();
  const loggedEmployer = getLoggedEmployer();

  return (
    <div className="min-h-screen flex flex-col bg-white">
      <main className="flex-1 p-4 md:p-6 lg:p-8 pb-24">
        <div className="max-w-screen-xl mx-auto">
          {/*  */}
          <header className="mb-8">
            <div className="flex items-center justify-between mb-1">
              <div className="flex items-center gap-3">            
                <button
                  onClick={handleIconClick}
                  className="flex items-center justify-center w-12 h-12 hover:bg-slate-200 rounded-lg transition-colors"
                >
                  <Layers size={24} className="text-slate-700" />
                </button>
                <div>
                  <h1 className="text-2xl md:text-3xl font-semibold text-gray-800">
                  {t('app.inspection')}                 
                  </h1>
                  <p className="text-slate-600">{loggedName && loggedEmployer ? `${loggedName}-${loggedEmployer}` : ''}</p>
                </div>
                
              </div>
              <div className="flex items-center gap-3">
                {/*  */}
                <button
                  onClick={toggleLanguage}
                  className="inline-flex items-center px-4 py-2 bg-white border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
                >
                  <Globe className="w-4 h-4 mr-2" />
                  {t(`language.${language}`)}
                </button>
                
                {/*  */}
                <button
                  onClick={() => {
                    //  session storage
                    sessionStorage.clear();
                    // 
                    window.location.reload();
                  }}
                  className="inline-flex items-center px-4 py-2 bg-white border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
                >
                  <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
                  </svg>
                  {t('logout')}
                </button>
              </div>
            </div>
            {/* <p className="text-gray-600 ml-9">
              {loggedName && loggedEmployer ? `${loggedName} - ${loggedEmployer}` : ''}
            </p> */}
          </header>

          {/*  */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-white rounded-lg shadow-sm border p-6">
              <div className="flex items-center">
                <div className="p-2 bg-blue-100 rounded-lg">
                  <Package className="w-6 h-6 text-blue-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600">{t('app.inspection.drugs')}</p>
                  <p className="text-2xl font-semibold text-gray-900">{totalDrugs}</p>
                </div>
              </div>
            </div>

            <div 
              className="bg-white rounded-lg shadow-sm border p-6 cursor-pointer hover:shadow-md hover:bg-gray-50 transition-all duration-200"
              onClick={() => setInspectionListOpen(true)}
            >
              <div className="flex items-center">
                <div className="p-2 bg-green-100 rounded-lg">
                  <TrendingUp className="w-6 h-6 text-green-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600">{t('app.inspection.total')}</p>
                  <p className="text-2xl font-semibold text-gray-900">{totalInspections}</p>
                </div>
              </div>
            </div>

            <div 
              className="bg-white rounded-lg shadow-sm border p-6 cursor-pointer hover:shadow-md hover:bg-gray-50 transition-all duration-200"
              onClick={() => {
                loadTodayInspectionCount(); // 
                setTodayInspectionListOpen(true);
              }}
            >
              <div className="flex items-center">
                <div className="p-2 bg-purple-100 rounded-lg">
                  <Package className="w-6 h-6 text-purple-600" />
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600">{t('app.inspection.today')}</p>
                  <p className="text-2xl font-semibold text-gray-900">{todayInspectionCount}</p>
                </div>
              </div>
            </div>
          </div>

          {/*  */}
          <DateRangeFilter
            startDate={startDate}
            endDate={endDate}
            onDateChange={handleDateChange}
            onSearch={handleSearch}
            onReset={handleReset}
            loading={loading}
          />

          {/*  */}
          <div className="mb-6">
            {/*  */}
            <div className="mb-4">
              <label className="inline-flex items-center">
                <input
                  type="checkbox"
                  checked={showLockedData}
                  onChange={(e) => setShowLockedData(e.target.checked)}
                  className="rounded border-gray-300 text-blue-600 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
                />
                <span className="ml-2 text-sm text-gray-700"></span>
              </label>
            </div>

            <div className="flex flex-col xl:flex-row gap-4 xl:items-end">
              {/*  */}
              <div className="flex flex-col sm:flex-row gap-3">
                <button
                  onClick={() => setBatchUploadOpen(true)}
                  className="inline-flex items-center px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
                >
                  <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                  </svg>
                  {t('app.inspection.batch_upload')}
                </button>
                
                <button
                  onClick={() => setExcelUploadOpen(true)}
                  className="inline-flex items-center px-4 py-2 bg-white border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
                >
                  <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                  </svg>
                  {t('app.inspection.excel_upload')}
                </button>
                
                <button
                  onClick={() => setManualCreateOpen(true)}
                  className="inline-flex items-center px-4 py-2 bg-white border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
                >
                  <Plus className="w-5 h-5 mr-2" />
                  {t('app.inspection.manual_create')}
                </button>
              </div>
              
              {/*  */}
              <div className="flex flex-col sm:flex-row gap-4 xl:flex-1 xl:justify-end">
                {/*  */}
                <div className="flex items-center gap-2">
                  <label className="text-sm font-medium text-gray-700 whitespace-nowrap">{t('app.inspection.barcode_search')}:</label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Search className="h-5 w-5 text-gray-400" />
                    </div>
                    <input
                      type="text"
                      value={searchBarcode}
                      onChange={(e) => handleSearchInputChange(e.target.value)}
                      onKeyPress={handleBarcodeKeyPress}
                      placeholder={t('app.inspection.search_barcode_placeholder')}
                      className="block w-full pl-10 pr-20 py-2 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                    <div className="absolute inset-y-0 right-0 flex items-center">
                      {searchLoading ? (
                        <div className="pr-3">
                          <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                        </div>
                      ) : (
                        <button
                          onClick={() => setBarcodeScannerOpen(true)}
                          className="pr-3 flex items-center justify-center text-blue-600 hover:text-blue-700 transition-colors"
                          title=""
                        >
                          <Camera className="h-5 w-5" />
                        </button>
                      )}
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="flex items-center gap-2">
                  <label className="text-sm font-medium text-gray-700 whitespace-nowrap">{t('app.inspection.number_search')}:</label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Search className="h-5 w-5 text-gray-400" />
                    </div>
                    <input
                      type="text"
                      value={searchNumber}
                      onChange={(e) => handleNumberSearchInputChange(e.target.value)}
                      placeholder={t('app.inspection.search_number_placeholder')}
                      className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-md leading-5 bg-white placeholder-gray-500 focus:outline-none focus:placeholder-gray-400 focus:ring-1 focus:ring-blue-500 focus:border-blue-500 sm:text-sm"
                    />
                    {numberSearchLoading && (
                      <div className="absolute inset-y-0 right-0 pr-3 flex items-center">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/*  */}
          {error && (
            <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-md">
              <div className="flex">
                <AlertCircle className="h-5 w-5 text-red-400" />
                <div className="ml-3">
                  <p className="text-sm text-red-800">{error}</p>
                </div>
              </div>
            </div>
          )}

          {/*  */}
          <InspectionTable
            data={filteredInspections}
            loading={loading}
            onRowClick={setSelectedDetail}
          />
        </div>
      </main>

      {/*  */}
      <footer className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 py-2 px-6 text-center text-gray-600 text-sm z-10">
        Copyright 2025 
      </footer>

      {/*  */}
      <InspectionDetail
        groupedData={selectedDetail}
        isOpen={!!selectedDetail}
        onClose={() => setSelectedDetail(null)}
        onDataUpdate={() => loadInspections()}
      />

      {/*  */}
      <BatchUploadModal
        isOpen={batchUploadOpen}
        onClose={() => setBatchUploadOpen(false)}
        onDataUpdate={() => {
          loadInspections();
          loadTodayInspectionCount();
        }}
      />

      {/* Excel  */}
      <ExcelUploadModal
        isOpen={excelUploadOpen}
        onClose={() => setExcelUploadOpen(false)}
        onDataUpdate={() => loadInspections()}
      />

      {/*  */}
      <ManualCreateModal
        isOpen={manualCreateOpen}
        onClose={() => setManualCreateOpen(false)}
        onDataUpdate={() => loadInspections()}
      />

      {/*  */}
      <InspectionListModal
        isOpen={inspectionListOpen}
        onClose={() => setInspectionListOpen(false)}
        inspections={inspections}
        initialDateRange={dateRange}
        onDateRangeChange={(startDate, endDate) => {
          setDateRange([startDate, endDate]);
          loadInspections(startDate, endDate);
        }}
        onDataUpdate={() => loadInspections()}
      />

      {/*  */}
      <InspectionListModal
        isOpen={todayInspectionListOpen}
        onClose={() => setTodayInspectionListOpen(false)}
        inspections={customTodayInspections}
        title=""
        isToday={true}
        onDateRangeChange={handleCustomDateRangeQuery}
        onDataUpdate={() => {
          const [startDate, endDate] = dateRange;
          loadInspections();
          handleCustomDateRangeQuery(startDate, endDate);
          // loadTodayInspectionCount();
        }}
      />

      {/*  */}
      <BarcodeScannerModal
        isOpen={barcodeScannerOpen}
        onClose={() => setBarcodeScannerOpen(false)}
        onBarcodeDetected={handleBarcodeScanned}
      />

      {/*  */}
      <CreateBarcodeModal
        isOpen={createBarcodeOpen}
        onClose={() => setCreateBarcodeOpen(false)}
        barcode={failedBarcode}
        onSuccess={() => {
          setCreateBarcodeOpen(false);
          if (searchBarcode) {
            handleBarcodeSearch(searchBarcode);
          }
        }}
      />
    </div>
  );
};