import React, { useState, useRef } from 'react';
import { Upload, X, Image, FileText, AlertCircle } from 'lucide-react';
import { InspectionContent, TextVisionResult } from '../../types/inspection';
import { analyzeShippingDocument } from '../../services/inspection';
import { getLoggedID, getLoggedName } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';
import { TextVisionResultModal } from './TextVisionResultModal';

interface UploadShippingDocumentModalProps {
  isOpen: boolean;
  onClose: () => void;
  inspectionItem: InspectionContent | null;
  onDataUpdate?: () => void;
}

export const UploadShippingDocumentModal: React.FC<UploadShippingDocumentModalProps> = ({
  isOpen,
  onClose,
  inspectionItem,
  onDataUpdate,
}) => {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [analysisResult, setAnalysisResult] = useState<TextVisionResult | null>(null);
  const [resultModalOpen, setResultModalOpen] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // 
  const resetForm = () => {
    setSelectedFile(null);
    setPreviewUrl(null);
    setError(null);
    setAnalysisResult(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  // 
  const handleClose = () => {
    resetForm();
    onClose();
  };

  // 
  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // 
    if (!file.type.startsWith('image/')) {
      setError('');
      return;
    }

    //  ( 10MB)
    if (file.size > 10 * 1024 * 1024) {
      setError(' 10MB');
      return;
    }

    setSelectedFile(file);
    setError(null);

    // 
    const reader = new FileReader();
    reader.onload = (e) => {
      setPreviewUrl(e.target?.result as string);
    };
    reader.readAsDataURL(file);
  };

  //  BASE64
  const convertToBase64 = (file: File): Promise<string> => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        const result = reader.result as string;
        //  data:image/...;base64, 
        const base64 = result.split(',')[1];
        resolve(base64);
      };
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  };

  // 
  const handleUpload = async () => {
    if (!selectedFile || !inspectionItem) return;

    const opId = getLoggedID();
    const opName = getLoggedName();

    if (!opId || !opName) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      //  BASE64
      const base64Image = await convertToBase64(selectedFile);

      console.log('=== Upload Shipping Document Debug ===');
      console.log('Selected file:', selectedFile.name, selectedFile.size, 'bytes');
      console.log('Base64 length:', base64Image.length);
      console.log('Operator ID:', opId);
      console.log('Operator Name:', opName);
      console.log('Inspection Item:', inspectionItem);

      //  API
      const response = await analyzeShippingDocument(
        inspectionItem,
        base64Image,
        opId,
        opName
      );

      console.log('=== API Response Debug ===');
      console.log('Response Code:', response.Code);
      console.log('Response Result:', response.Result);
      console.log('Response Data:', response.Data);
      console.log('Full Response:', JSON.stringify(response, null, 2));
      console.log('=====================================');

      if (response.Code === 200 && response.Data && response.Data.length > 0) {
        console.log(' Analysis successful, opening result modal');
        setAnalysisResult(response.Data[0]);
        setResultModalOpen(true);
        // 
      } else {
        console.log(' Analysis failed or no data returned');
        setError(response.Result || '');
      }
    } catch (err) {
      console.error('Upload and analyze error:', err);
      console.log(' Exception occurred during upload/analysis');
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen || !inspectionItem) return null;

  return (
    <>
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
        <div className="bg-white rounded-lg shadow-xl max-w-md w-full max-h-[90vh] overflow-y-auto">
          <div className="flex items-center justify-between p-6 border-b">
            <div>
              <h3 className="text-lg font-semibold text-gray-900 flex items-center">
                <Upload className="w-5 h-5 mr-2" />
                
              </h3>
              <p className="text-sm text-gray-600 mt-1">
                {inspectionItem.CHT_NAME || inspectionItem.NAME}
              </p>
            </div>
            <button
              onClick={handleClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <X className="w-6 h-6" />
            </button>
          </div>

          <div className="p-6">
            {error && (
              <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
                <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
                {error}
              </div>
            )}

            <div className="space-y-4">
              {/*  */}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  
                </label>
                <div
                  className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-gray-400 transition-colors cursor-pointer"
                  onClick={() => fileInputRef.current?.click()}
                >
                  <input
                    ref={fileInputRef}
                    type="file"
                    accept="image/*"
                    onChange={handleFileSelect}
                    className="hidden"
                  />
                  
                  {previewUrl ? (
                    <div className="space-y-2">
                      <img
                        src={previewUrl}
                        alt=""
                        className="max-w-full max-h-48 mx-auto rounded-lg shadow-sm"
                      />
                      <p className="text-sm text-gray-600">
                        {selectedFile?.name}
                      </p>
                      <p className="text-xs text-gray-500">
                        
                      </p>
                    </div>
                  ) : (
                    <div className="space-y-2">
                      <Image className="w-12 h-12 text-gray-400 mx-auto" />
                      <div>
                        <p className="text-sm text-gray-600"></p>
                        <p className="text-xs text-gray-500"> JPG, PNG, GIF  10MB</p>
                      </div>
                    </div>
                  )}
                </div>
              </div>

              {/*  */}
              <div className="bg-gray-50 rounded-lg p-4">
                <h4 className="text-sm font-medium text-gray-700 mb-2 flex items-center">
                  <FileText className="w-4 h-4 mr-1" />
                  
                </h4>
                <div className="space-y-1 text-sm text-gray-600">
                  <div>: {inspectionItem.IC_SN}</div>
                  <div>: {inspectionItem.CODE}</div>
                  <div>: {inspectionItem.SKDIACODE}</div>
                  <div>: {inspectionItem.START_QTY} {inspectionItem.PAKAGE}</div>
                </div>
              </div>
            </div>

            <div className="flex gap-3 mt-6">
              <button
                type="button"
                onClick={handleClose}
                className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
                disabled={loading}
              >
                
              </button>
              <button
                onClick={handleUpload}
                disabled={loading || !selectedFile}
                className="flex-1 px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
              >
                {loading ? (
                  <>
                    <LoadingSpinner size="small" className="mr-2" />
                    ...
                  </>
                ) : (
                  <>
                    <Upload className="w-4 h-4 mr-2" />
                    
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <TextVisionResultModal
        isOpen={resultModalOpen}
        onClose={() => {
          setResultModalOpen(false);
          setAnalysisResult(null);
          handleClose(); // 
        }}
        result={analysisResult}
        onConfirmSubmit={() => {
          //  - 
          if (onDataUpdate) {
            onDataUpdate();
          }
          setResultModalOpen(false);
          setAnalysisResult(null);
          handleClose();
        }}
      />
    </>
  );
};