import React, { useState } from 'react';
import { Trash2, AlertTriangle, X } from 'lucide-react';
import { deleteInspectionContent } from '../../services/inspection';
import { InspectionContent } from '../../types/inspection';
import LoadingSpinner from '../common/LoadingSpinner';

interface DeleteInspectionContentModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfirm: () => void;
  inspectionContent: InspectionContent | null;
}

export const DeleteInspectionContentModal: React.FC<DeleteInspectionContentModalProps> = ({
  isOpen,
  onClose,
  onConfirm,
  inspectionContent,
}) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleConfirm = async () => {
    if (!inspectionContent) return;

    setLoading(true);
    setError(null);

    try {
      const response = await deleteInspectionContent(inspectionContent.GUID);

      if (response.Code === 200) {
        onConfirm();
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error('Delete inspection content error:', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen || !inspectionContent) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full">
        <div className="flex items-center justify-between p-6 border-b">
          <div className="flex items-center">
            <div className="flex-shrink-0 w-10 h-10 mx-auto bg-red-100 rounded-full flex items-center justify-center">
              <AlertTriangle className="w-6 h-6 text-red-600" />
            </div>
            <div className="ml-4">
              <h3 className="text-lg font-semibold text-gray-900">
                
              </h3>
            </div>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
            disabled={loading}
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <div className="p-6">
          {error && (
            <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          <div className="mb-6">
            <p className="text-sm text-gray-600 mb-4">
              
            </p>
            
            <div className="bg-gray-50 rounded-lg p-4 space-y-2">
              <div className="flex justify-between">
                <span className="text-sm text-gray-500">:</span>
                <span className="text-sm font-medium text-gray-900">{inspectionContent.IC_SN}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-500">:</span>
                <span className="text-sm font-medium text-gray-900">{inspectionContent.PON}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-500">:</span>
                <span className="text-sm font-medium text-gray-900">{inspectionContent.CHT_NAME || inspectionContent.NAME}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-500">:</span>
                <span className="text-sm font-medium text-gray-900">{inspectionContent.CODE}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-500">:</span>
                <span className="text-sm font-medium text-gray-900">{inspectionContent.START_QTY} {inspectionContent.PAKAGE}</span>
              </div>
            </div>
          </div>

          <div className="flex gap-3">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
              disabled={loading}
            >
              
            </button>
            <button
              onClick={handleConfirm}
              disabled={loading}
              className="flex-1 px-4 py-2 bg-red-600 text-white rounded-md hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center"
            >
              {loading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Trash2 className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};