import React from 'react';
import { useState, useCallback } from 'react';
import { GroupedInspection, InspectionContent } from '../../types/inspection';
import { Calendar, User, Package2, AlertCircle, FileText, Building2, Hash, Pill, Plus, Trash2, Eye } from 'lucide-react';
import { AddSubContentModal } from './AddSubContentModal';
import { DeleteConfirmModal } from './DeleteConfirmModal';
import { EditSubContentModal } from './EditSubContentModal';
import { UploadShippingDocumentModal } from './UploadShippingDocumentModal';
import { DeleteInspectionContentModal } from './DeleteInspectionContentModal';
import { getTextVisionByMasterGUID, updateSubContent } from '../../services/inspection';
import { getLoggedLevel } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';

interface InspectionDetailProps {
  groupedData: GroupedInspection | null;
  isOpen: boolean;
  onClose: () => void;
  onDataUpdate?: () => void;
}

interface ImageViewModalProps {
  isOpen: boolean;
  onClose: () => void;
  imageUrl: string;
  title: string;
}

const ImageViewModal: React.FC<ImageViewModalProps> = ({ isOpen, onClose, imageUrl, title }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center p-4 z-[60]">
      <div className="relative max-w-[90vw] max-h-[90vh]">
        <button
          onClick={onClose}
          className="absolute -top-10 right-0 text-white hover:text-gray-300 transition-colors"
        >
          <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
        <img
          src={imageUrl}
          alt={title}
          className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
        />
        <div className="absolute bottom-0 left-0 right-0 bg-black bg-opacity-50 text-white p-2 rounded-b-lg">
          <p className="text-center text-sm">{title}</p>
        </div>
      </div>
    </div>
  );
};

export const InspectionDetail: React.FC<InspectionDetailProps> = ({
  groupedData,
  isOpen,
  onClose,
  onDataUpdate,
}) => {
  const [addModalOpen, setAddModalOpen] = useState(false);
  const [selectedItem, setSelectedItem] = useState<InspectionContent | null>(null);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [selectedSubContentForEdit, setSelectedSubContentForEdit] = useState<any>(null);
  const [selectedSubContent, setSelectedSubContent] = useState<{
    subContentGuid: string;
    masterGuid: string;
    itemName: string;
    lot: string;
    qty: string;
  } | null>(null);
  const [uploadModalOpen, setUploadModalOpen] = useState(false);
  const [selectedItemForUpload, setSelectedItemForUpload] = useState<InspectionContent | null>(null);
  const [deleteInspectionModalOpen, setDeleteInspectionModalOpen] = useState(false);
  const [selectedItemForDelete, setSelectedItemForDelete] = useState<InspectionContent | null>(null);
  const [imageViewModal, setImageViewModal] = useState<{ isOpen: boolean; imageUrl: string; title: string }>({
    isOpen: false,
    imageUrl: '',
    title: ''
  });
  const [imageLoading, setImageLoading] = useState<string | null>(null);
  const [lockingItems, setLockingItems] = useState<Set<string>>(new Set());
  const [error, setError] = useState<string | null>(null);

  // 
  const userLevel = getLoggedLevel();
  const canLockUnlock = userLevel === '20';

  const handleAddSubContent = (item: InspectionContent) => {
    setSelectedItem(item);
    setAddModalOpen(true);
  };

  const handleUploadShippingDocument = (item: InspectionContent) => {
    setSelectedItemForUpload(item);
    setUploadModalOpen(true);
  };

  const handleAddSuccess = () => {
    if (onDataUpdate) {
      onDataUpdate();
    }
    // 
    setAddModalOpen(false);
  };

  const handleEditSubContent = useCallback((subContent: any) => {
    setSelectedSubContentForEdit(subContent);
    setEditModalOpen(true);
  }, []);

  const handleEditSuccess = useCallback(() => {
    if (onDataUpdate) {
      onDataUpdate();
    }
    setEditModalOpen(false);
    setSelectedSubContentForEdit(null);
  }, [onDataUpdate]);

  const handleDeleteSubContent = useCallback((
    subContentGuid: string,
    masterGuid: string,
    itemName: string,
    lot: string,
    qty: string
  ) => {
    setSelectedSubContent({
      subContentGuid,
      masterGuid,
      itemName,
      lot,
      qty
    });
    setDeleteModalOpen(true);
  }, []);

  const handleDeleteSuccess = useCallback(() => {
    if (onDataUpdate) {
      onDataUpdate();
    }
    setDeleteModalOpen(false);
    setSelectedSubContent(null);
  }, [onDataUpdate]);

  const handleDeleteInspectionContent = (item: InspectionContent) => {
    setSelectedItemForDelete(item);
    setDeleteInspectionModalOpen(true);
  };

  const handleDeleteInspectionSuccess = () => {
    if (onDataUpdate) {
      onDataUpdate();
    }
    setDeleteInspectionModalOpen(false);
    setSelectedItemForDelete(null);
    onClose(); // 
  };

  // 
  const handleViewImage = async (itemGuid: string, itemName: string) => {
    setImageLoading(itemGuid);
    
    try {
      console.log('===  ===');
      console.log('Item GUID:', itemGuid);
      
      const response = await getTextVisionByMasterGUID(itemGuid);
      
      console.log('API Response:', response);
      console.log('API Response Data:', JSON.stringify(response.Data, null, 2));
      
      if (response.Code === 200 && response.Data) {
        const imageData = response.Data;
        console.log('Image Data:', imageData);
        console.log('Base64 field exists:', !!imageData.base64);
        console.log('Base64 length:', imageData.base64 ? imageData.base64.length : 0);
        
        if (imageData.base64) {
          const imageUrl = imageData.base64.startsWith('data:') 
            ? imageData.base64 
            : `data:image/jpeg;base64,${imageData.base64}`;
          
          console.log('Final image URL (first 100 chars):', imageUrl.substring(0, 100));
          
          setImageViewModal({
            isOpen: true,
            imageUrl: imageUrl,
            title: `${itemName} - `
          });
        } else {
          console.log('No base64 data found in response');
          console.log('Available fields:', Object.keys(imageData));
          alert('');
        }
      } else {
        console.log('API response failed or no data');
        alert('');
      }
    } catch (err) {
      console.error(':', err);
      alert('');
    } finally {
      setImageLoading(null);
    }
  };

  // /
  const handleLockUnlock = async (subContent: any, newState: '' | '') => {
    if (!canLockUnlock) {
      setError('');
      return;
    }

    setLockingItems(prev => new Set([...prev, subContent.GUID]));
    setError(null);

    try {
      console.log('=== Lock/Unlock Sub Content Debug ===');
      console.log('Sub Content GUID:', subContent.GUID);
      console.log('Current State:', subContent.STATE);
      console.log('New State:', newState);

      //  STATE 
      const updateData = {
        ...subContent,
        STATE: newState,
        //  OP_TIME  service 
      };

      console.log('Update Data:', JSON.stringify(updateData, null, 2));

      const response = await updateSubContent(updateData);

      console.log('Lock/Unlock Response:', JSON.stringify(response, null, 2));

      if (response.Code === 200) {
        console.log(` ${newState === '' ? '' : ''}`);
        
        // 
        if (onDataUpdate) {
          onDataUpdate();
        }
      } else {
        setError(response.Result || `${newState === '' ? '' : ''}`);
      }
    } catch (err) {
      console.error('Lock/Unlock error:', err);
      setError(err instanceof Error ? err.message : `${newState === '' ? '' : ''}`);
    } finally {
      setLockingItems(prev => {
        const newSet = new Set(prev);
        newSet.delete(subContent.GUID);
        return newSet;
      });
    }
  };

  if (!isOpen || !groupedData) return null;

  return (
    <>
      <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
        <div className="bg-white rounded-lg shadow-xl max-w-7xl w-full max-h-[90vh] overflow-hidden">
          <div className="flex items-center justify-between p-6 border-b bg-gray-50">
            <div>
              <h3 className="text-lg font-semibold text-gray-900 mb-2"></h3>
              <div className="flex items-center gap-4 text-sm text-gray-600">
                <div className="flex items-center">
                  : {groupedData.CODE}
                </div>
                <div className="flex items-center">
                  : {groupedData.SKDIACODE}
                </div>
                <div className="flex items-center">
                  : {groupedData.BRD || ''}
                </div>
              </div>
              {error && (
                <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
                  {error}
                </div>
              )}
            </div>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>
          
          <div className="p-6 overflow-y-auto max-h-[calc(90vh-120px)]">
            {groupedData.items.length === 0 ? (
              <div className="text-center py-8">
                <Package2 className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-600"></p>
              </div>
            ) : (
              <div className="space-y-6">
                {groupedData.items.map((item: InspectionContent) => (
                  <div key={item.GUID} className="bg-gray-50 rounded-lg p-6 border">
                    {/*  */}
                    <div className="flex items-center justify-between mb-4">
                      <h4 className="text-lg font-medium text-gray-900">
                        : {item.PON}
                      </h4>
                      <div className="flex gap-2">
                        {/*  */}
                        <button
                          onClick={() => handleAddSubContent(item)}
                          className="inline-flex items-center px-4 py-2 bg-blue-600 text-white text-sm font-medium rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
                        >
                          <Plus className="w-4 h-4 mr-2" />
                          
                        </button>
                        
                        {/*  */}
                        <button
                          onClick={() => handleUploadShippingDocument(item)}
                          className="inline-flex items-center px-4 py-2 bg-white border border-gray-300 text-gray-700 text-sm font-medium rounded-md hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-colors"
                        >
                          <svg className="w-4 h-4 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                          </svg>
                          
                        </button>
                        
                        {/*  */}
                        <button
                          onClick={() => handleDeleteInspectionContent(item)}
                          className="inline-flex items-center px-3 py-2 bg-white border border-red-300 text-red-700 text-sm font-medium rounded-md hover:bg-red-50 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 transition-colors"
                          title=""
                        >
                          <Trash2 className="w-4 h-4" />
                        </button>
                      </div>
                    </div>

                    {/*  */}
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                      <div>
                        <h4 className="font-medium text-gray-900 mb-3 flex items-center">
                          <FileText className="w-4 h-4 mr-2" />
                          
                        </h4>
                        <div className="space-y-2 text-sm">
                          <div><span className="text-gray-500">:</span> <span className="font-medium">{item.IC_SN}</span></div>
                          <div><span className="text-gray-500">:</span> <span className="font-medium text-blue-600">{item.START_QTY} {item.PAKAGE}</span></div>
                          {item.END_QTY && (
                            <div><span className="text-gray-500">:</span> <span className="font-medium text-green-600">{item.END_QTY} {item.PAKAGE}</span></div>
                          )}
                        </div>
                      </div>
                      
                      <div>
                        <h4 className="font-medium text-gray-900 mb-3 flex items-center">
                          <Calendar className="w-4 h-4 mr-2" />
                          
                        </h4>
                        <div className="space-y-2 text-sm">
                          <div><span className="text-gray-500">:</span> <span className="font-medium">{item.ADD_TIME}</span></div>
                          <div><span className="text-gray-500">:</span> <span className="font-medium">{item.DELIVERY_TIME ? item.DELIVERY_TIME.split(' ')[0].replace(/-/g, '/') : ''}</span></div>
                        </div>
                      </div>

                      <div>
                        <h4 className="font-medium text-gray-900 mb-3 flex items-center">
                          <AlertCircle className="w-4 h-4 mr-2" />
                          
                        </h4>
                        <div className="space-y-2 text-sm">
                          {item.SEQ && <div><span className="text-gray-500">:</span> {item.SEQ}</div>}
                          {item.FREE_CHARGE_FLAG && <div><span className="text-gray-500">:</span> {item.FREE_CHARGE_FLAG}</div>}
                          {item.NOTE && <div><span className="text-gray-500">:</span> {item.NOTE}</div>}
                        </div>
                      </div>
                    </div>

                    {/*  */}
                    {item.Sub_content && item.Sub_content.length > 0 && (
                      <div className="border-t pt-4 mt-4">
                        <h5 className="font-medium text-gray-900 mb-3"></h5>
                        <div className="space-y-3">
                          {item.Sub_content.map((subItem) => (
                            <div key={subItem.GUID} className="bg-white rounded-md p-4 border border-gray-200">
                              <div className="flex justify-between items-start mb-3">
                                <div className="flex gap-2">
                                  {subItem.STATE !== '' && (
                                    <>
                                      <button
                                        onClick={() => handleEditSubContent(subItem)}
                                        className="inline-flex items-center px-2 py-1 text-xs font-medium text-gray-600 bg-white border border-gray-300 rounded hover:bg-gray-50 focus:outline-none focus:ring-1 focus:ring-blue-500 transition-colors"
                                        title=""
                                      >
                                        <svg className="w-3 h-3 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
                                        </svg>
                                        
                                      </button>
                                      <button
                                        onClick={() => handleDeleteSubContent(
                                          subItem.GUID,
                                          subItem.Master_GUID,
                                          item.CHT_NAME || item.NAME,
                                          subItem.LOT,
                                          `${subItem.END_QTY}`
                                        )}
                                        className="inline-flex items-center px-2 py-1 text-xs font-medium text-red-600 bg-white border border-red-300 rounded hover:bg-red-50 focus:outline-none focus:ring-1 focus:ring-red-500 transition-colors"
                                        title=""
                                      >
                                        <Trash2 className="w-3 h-3" />
                                      </button>
                                    </>
                                  )}
                                  {canLockUnlock && (
                                    <div className="flex gap-1">
                                      {subItem.STATE === '' ? (
                                        <button
                                          onClick={() => handleLockUnlock(subItem, '')}
                                          disabled={lockingItems.has(subItem.GUID)}
                                          className="inline-flex items-center px-2 py-1 bg-red-600 text-white text-xs rounded hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                          title=""
                                        >
                                          {lockingItems.has(subItem.GUID) ? (
                                            <LoadingSpinner size="small" className="w-3 h-3" />
                                          ) : (
                                            <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 15v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2zm10-10V7a4 4 0 00-8 0v4h8z" />
                                            </svg>
                                          )}
                                        </button>
                                      ) : (
                                        <button
                                          onClick={() => handleLockUnlock(subItem, '')}
                                          disabled={lockingItems.has(subItem.GUID)}
                                          className="inline-flex items-center px-2 py-1 bg-green-600 text-white text-xs rounded hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
                                          title=""
                                        >
                                          {lockingItems.has(subItem.GUID) ? (
                                            <LoadingSpinner size="small" className="w-3 h-3" />
                                          ) : (
                                            <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 11V7a4 4 0 118 0m-4 8v2m-6 4h12a2 2 0 002-2v-6a2 2 0 00-2-2H6a2 2 0 00-2 2v6a2 2 0 002 2z" />
                                            </svg>
                                          )}
                                        </button>
                                      )}
                                    </div>
                                  )}
                                  <button
                                    onClick={() => handleViewImage(item.GUID, item.CHT_NAME || item.NAME)}
                                    disabled={imageLoading === item.GUID}
                                    className="inline-flex items-center px-2 py-1 text-xs font-medium text-blue-600 bg-white border border-blue-300 rounded hover:bg-blue-50 focus:outline-none focus:ring-1 focus:ring-blue-500 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                                    title=""
                                  >
                                    {imageLoading === item.GUID ? (
                                      <LoadingSpinner size="small" className="w-3 h-3" />
                                    ) : (
                                      <Eye className="w-3 h-3" />
                                    )}
                                  </button>
                                </div>
                              </div>

                              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                                <div>
                                  <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                  <div className="text-sm space-y-1">
                                    <div><span className="text-gray-500">:</span> <span className="font-medium text-green-600">{subItem.END_QTY} {subItem.PAKAGE}</span></div>
                                  </div>
                                </div>
                                
                                <div>
                                  <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                  <div className="text-sm space-y-1">
                                    <div><span className="text-gray-500">:</span> {subItem.VAL ? new Date(subItem.VAL).toLocaleDateString('zh-TW', { year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//g, '/') : ''}</div>
                                    <div><span className="text-gray-500">:</span> {subItem.LOT}</div>
                                  </div>
                                </div>
                                
                                <div>
                                  <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                  <div className="text-sm space-y-1">
                                    <div className="flex items-center">
                                      <User className="w-3 h-3 text-gray-400 mr-1" />
                                      {subItem.OP}
                                    </div>
                                    <div className="flex items-center">
                                      <Calendar className="w-3 h-3 text-gray-400 mr-1" />
                                      {subItem.OP_TIME ? new Date(subItem.OP_TIME).toLocaleDateString('zh-TW', { year: 'numeric', month: '2-digit', day: '2-digit' }).replace(/\//g, '/') : ''}
                                    </div>
                                  </div>
                                </div>
                                
                                <div>
                                  <h6 className="text-xs font-medium text-gray-500 uppercase mb-1"></h6>
                                  <div className="text-sm">
                                    <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${
                                      subItem.STATE === '' 
                                        ? 'bg-yellow-100 text-yellow-800' 
                                        : 'bg-green-100 text-green-800'
                                    }`}>
                                      {subItem.STATE}
                                    </span>
                                  </div>
                                </div>
                              </div>
                              
                              {(subItem.BARCODE1 || subItem.BARCODE2 || subItem.NOTE) && (
                                <div className="mt-3 pt-3 border-t border-gray-100">
                                  {(subItem.BARCODE1 || subItem.BARCODE2) && (
                                    <div className="mb-2">
                                      <span className="text-xs font-medium text-gray-500 uppercase"></span>
                                      <div className="text-sm text-gray-600 mt-1">
                                        {subItem.BARCODE1 && <div>1: {subItem.BARCODE1}</div>}
                                        {subItem.BARCODE2 && <div>2: {subItem.BARCODE2}</div>}
                                      </div>
                                    </div>
                                  )}
                                  {subItem.NOTE && (
                                    <div>
                                      <span className="text-xs font-medium text-gray-500 uppercase"></span>
                                      <p className="text-sm text-gray-600 mt-1">{subItem.NOTE}</p>
                                    </div>
                                  )}
                                </div>
                              )}
                            </div>
                          ))}
                        </div>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>

      {/*  */}
      <AddSubContentModal
        isOpen={addModalOpen}
        onClose={() => setAddModalOpen(false)}
        inspectionItem={selectedItem}
        onSuccess={handleAddSuccess}
      />

      {/*  */}
      <UploadShippingDocumentModal
        isOpen={uploadModalOpen}
        onClose={() => {
          setUploadModalOpen(false);
          setSelectedItemForUpload(null);
        }}
        inspectionItem={selectedItemForUpload}
        onDataUpdate={onDataUpdate}
      />

      {/*  */}
      <EditSubContentModal
        isOpen={editModalOpen}
        onClose={() => {
          setEditModalOpen(false);
          setSelectedSubContentForEdit(null);
        }}
        subContentData={selectedSubContentForEdit}
        onSuccess={handleEditSuccess}
        inspectionItem={groupedData.items.find(item => 
          item.Sub_content?.some(sub => sub.GUID === selectedSubContentForEdit?.GUID)
        )}
      />

      {/*  */}
      <DeleteConfirmModal
        isOpen={deleteModalOpen}
        onClose={() => {
          setDeleteModalOpen(false);
          setSelectedSubContent(null);
        }}
        onConfirm={handleDeleteSuccess}
        subContentData={selectedSubContent}
      />

      {/*  */}
      <DeleteInspectionContentModal
        isOpen={deleteInspectionModalOpen}
        onClose={() => {
          setDeleteInspectionModalOpen(false);
          setSelectedItemForDelete(null);
        }}
        onConfirm={handleDeleteInspectionSuccess}
        inspectionContent={selectedItemForDelete}
      />

      {/*  */}
      <ImageViewModal
        isOpen={imageViewModal.isOpen}
        onClose={() => setImageViewModal({ isOpen: false, imageUrl: '', title: '' })}
        imageUrl={imageViewModal.imageUrl}
        title={imageViewModal.title}
      />
    </>
  );
};