interface Config {
  domain: string;
  homepage: string;
  ai: string;
}

let config: Config | null = null;

export const loadConfig = async (): Promise<Config> => {
  if (config) return config;
  
  try {
    const response = await fetch('/config.txt');
    if (!response.ok) {
      throw new Error('Failed to load configuration');
    }
    config = await response.json();
    return config;
  } catch (error) {
    console.error('Error loading config:', error);
    throw error;
  }
};

export const getApiUrl = (path: string): string => {
  if (!config) {
    throw new Error('Configuration not loaded. Call loadConfig() first.');
  }
  
  // Remove leading slash from path if present
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  
  // Ensure domain doesn't end with slash and path is properly joined
  const baseUrl = config.domain.endsWith('/') ? config.domain.slice(0, -1) : config.domain;
  
  return `${baseUrl}/${cleanPath}`;
};

export const getAiUrl = (path: string): string => {
  if (!config) {
    throw new Error('Configuration not loaded. Call loadConfig() first.');
  }
  
  // Remove leading slash from path if present
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  
  // Ensure domain doesn't end with slash and path is properly joined
  const baseUrl = config.ai.endsWith('/') ? config.ai.slice(0, -1) : config.ai;
  
  return `${baseUrl}/${cleanPath}`;
};

export const getConfig = (): Config | null => {
  return config;
};
