import { websocketService } from '../services/websocket';

const BROADCAST_METHODS = [
  'sub_content_add',
  'sub_content_update',
  'sub_contents_delete_by_GUID',
];

export const shouldTriggerBroadcast = (response: any): boolean => {
  if (!response || !response.Method) {
    return false;
  }

  return BROADCAST_METHODS.includes(response.Method);
};

export const triggerWebSocketBroadcast = async (response: any): Promise<void> => {
  if (!shouldTriggerBroadcast(response)) {
    return;
  }

  try {
    console.log('=== Trigger WebSocket Broadcast ===');
    console.log('Method:', response.Method);
    console.log('Response:', response);

    await websocketService.sendMessage();
    console.log(' WebSocket broadcast triggered successfully');
  } catch (error) {
    console.error(' Failed to trigger WebSocket broadcast:', error);
  }
};
