import React, { useState, useEffect } from 'react';
import { X, Package, CheckCircle, AlertCircle, Link } from 'lucide-react';
import { InspectionContent } from '../../types/inspection';
import { getInspectionByIcSn, updateTextVision } from '../../services/inspection';
import LoadingSpinner from '../common/LoadingSpinner';

interface PairingModalProps {
  isOpen: boolean;
  onClose: () => void;
  analysisResult: any;
  onSuccess: (resultGuid: string) => void;
}

export const PairingModal: React.FC<PairingModalProps> = ({
  isOpen,
  onClose,
  analysisResult,
  onSuccess,
}) => {
  const [inspectionItems, setInspectionItems] = useState<InspectionContent[]>([]);
  const [selectedItem, setSelectedItem] = useState<InspectionContent | null>(null);
  const [loading, setLoading] = useState(false);
  const [searchLoading, setSearchLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // 
  useEffect(() => {
    if (isOpen && analysisResult?.po_num) {
      searchInspectionData();
    }
  }, [isOpen, analysisResult]);

  const searchInspectionData = async () => {
    if (!analysisResult?.po_num) return;

    setSearchLoading(true);
    setError(null);

    try {
      console.log(':', analysisResult.po_num);
      const response = await getInspectionByIcSn(analysisResult.po_num);

      if (response.Code === 200 && response.Data) {
        setInspectionItems(response.Data);
        console.log(':', response.Data.length, '');
        
        // 
        const filteredItems = response.Data.filter((item: InspectionContent) => {
          const hasSubContent = item.Sub_content && item.Sub_content.length > 0;
          if (hasSubContent) {
            console.log(':', item.IC_SN, ':', item.Sub_content.length);
          }
          return !hasSubContent;
        });
        
        setInspectionItems(filteredItems);
        console.log(':', filteredItems.length, '');
      } else {
        setError(response.Result || '');
        setInspectionItems([]);
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
      setInspectionItems([]);
    } finally {
      setSearchLoading(false);
    }
  };

  const handlePairing = async () => {
    if (!selectedItem || !analysisResult) return;

    setLoading(true);
    setError(null);

    try {
      console.log(':', {
        selectedItem: selectedItem.GUID,
        analysisResult: analysisResult.GUID
      });

      const response = await updateTextVision(
        analysisResult.GUID,
        selectedItem,
        analysisResult
      );

      console.log(' API :', response);

      if (response.Code === 200) {
        console.log('');
        //  GUID
        onSuccess(analysisResult.GUID);
      } else {
        setError(response.Result || '');
      }
    } catch (err) {
      console.error(':', err);
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  const formatExpiryDate = (dateStr: string): string => {
    if (!dateStr) return '';
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('zh-TW', { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit' 
      }).replace(/\//g, '/');
    } catch {
      return dateStr;
    }
  };

  if (!isOpen || !analysisResult) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-6xl w-full max-h-[90vh] overflow-hidden">
        <div className="flex items-center justify-between p-6 border-b bg-gray-50">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              <Link className="w-5 h-5 mr-2" />
              
            </h3>
            <p className="text-sm text-gray-600 mt-1">
              : {analysisResult.po_num}
            </p>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        <div className="p-6 overflow-y-auto max-h-[calc(90vh-200px)]">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/*  */}
            <div>
              <h4 className="text-md font-medium text-gray-900 mb-4"></h4>
              
              <div className="bg-blue-50 rounded-lg p-4 mb-4">
                {analysisResult.base64 && (
                  <img
                    src={analysisResult.base64.startsWith('data:') ? analysisResult.base64 : `data:image/jpeg;base64,${analysisResult.base64}`}
                    alt=""
                    className="w-full h-48 object-cover rounded mb-4"
                  />
                )}
                
                <div className="space-y-3">
                  <div className="bg-white rounded p-3">
                    <h5 className="text-sm font-medium text-gray-700 mb-2"></h5>
                    <div className="space-y-1 text-sm">
                      <div><span className="text-gray-500">:</span> {analysisResult.code}</div>
                      <div><span className="text-gray-500">:</span> {analysisResult.cht_name}</div>
                      <div><span className="text-gray-500">:</span> {analysisResult.name}</div>
                    </div>
                  </div>
                  
                  <div className="bg-white rounded p-3">
                    <h5 className="text-sm font-medium text-gray-700 mb-2"></h5>
                    <div className="space-y-1 text-sm">
                      <div><span className="text-gray-500">:</span> {analysisResult.batch_num}</div>
                      <div><span className="text-gray-500">:</span> {formatExpiryDate(analysisResult.expirydate)}</div>
                      <div><span className="text-gray-500">:</span> {analysisResult.qty}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/*  */}
            <div>
              <h4 className="text-md font-medium text-gray-900 mb-4">
                
                {searchLoading && <LoadingSpinner size="small" className="ml-2" />}
              </h4>

              {error && (
                <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
                  <AlertCircle className="w-4 h-4 mr-2 flex-shrink-0" />
                  {error}
                </div>
              )}

              {searchLoading ? (
                <div className="text-center py-8">
                  <LoadingSpinner size="medium" className="mx-auto mb-4" />
                  <p className="text-gray-600">...</p>
                </div>
              ) : inspectionItems.length === 0 ? (
                <div className="text-center py-8">
                  <Package className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                  <p className="text-gray-600"></p>
                </div>
              ) : (
                <div className="space-y-3">
                  {inspectionItems.map((item) => (
                    <div
                      key={item.GUID}
                      className={`border rounded-lg p-4 cursor-pointer transition-colors ${
                        selectedItem?.GUID === item.GUID
                          ? 'border-blue-500 bg-blue-50'
                          : 'border-gray-200 hover:border-gray-300'
                      }`}
                      onClick={() => setSelectedItem(item)}
                    >
                      <div className="flex items-start justify-between mb-2">
                        <div className="flex-1">
                          <h5 className="font-medium text-gray-900">{item.CHT_NAME || item.NAME}</h5>
                          <p className="text-sm text-gray-600">: {item.IC_SN}</p>
                        </div>
                        {selectedItem?.GUID === item.GUID && (
                          <CheckCircle className="w-5 h-5 text-blue-500 flex-shrink-0" />
                        )}
                      </div>
                      
                      <div className="grid grid-cols-2 gap-4 text-sm">
                        <div>
                          <span className="text-gray-500">:</span>
                          <span className="ml-1 font-medium">{item.CODE}</span>
                        </div>
                        <div>
                          <span className="text-gray-500">:</span>
                          <span className="ml-1 font-medium">{item.SKDIACODE}</span>
                        </div>
                        <div>
                          <span className="text-gray-500">:</span>
                          <span className="ml-1 font-medium">{item.START_QTY} {item.PAKAGE}</span>
                        </div>
                        <div>
                          <span className="text-gray-500">:</span>
                          <span className="ml-1 font-medium">{item.BRD || ''}</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        <div className="flex justify-end gap-3 p-6 border-t bg-gray-50">
          <button
            onClick={onClose}
            className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50 transition-colors"
            disabled={loading}
          >
            
          </button>
          <button
            onClick={handlePairing}
            disabled={loading || !selectedItem}
            className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center"
          >
            {loading ? (
              <>
                <LoadingSpinner size="small" className="mr-2" />
                ...
              </>
            ) : (
              <>
                <Link className="w-4 h-4 mr-2" />
                
              </>
            )}
          </button>
        </div>
      </div>
    </div>
  );
};