import * as signalR from '@microsoft/signalr';
import { getConfig } from '../config';

class WebSocketService {
  private connection: signalR.HubConnection | null = null;
  private messageCallbacks: Array<(message: any) => void> = [];
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private isIntentionallyClosed = false;

  connect() {
    if (this.connection && this.connection.state === signalR.HubConnectionState.Connected) {
      console.log('SignalR already connected');
      return;
    }

    try {
      const config = getConfig();
      const hubUrl = `${config.domain}/chatHub`;

      console.log('=== SignalR Connection ===');
      console.log('URL:', hubUrl);

      this.isIntentionallyClosed = false;

      this.connection = new signalR.HubConnectionBuilder()
        .withUrl(hubUrl)
        .withAutomaticReconnect()
        .configureLogging(signalR.LogLevel.Information)
        .build();

      this.connection.on('ReceiveMessage', (message: any) => {
        console.log('=== SignalR ReceiveMessage ===');
        console.log('Message:', message);

        this.messageCallbacks.forEach(callback => {
          try {
            callback(message);
          } catch (error) {
            console.error('Error in message callback:', error);
          }
        });
      });

      this.connection.onclose((error) => {
        console.warn('SignalR connection closed:', error);

        if (!this.isIntentionallyClosed && this.reconnectAttempts < this.maxReconnectAttempts) {
          this.reconnectAttempts++;
          console.log(`Attempting to reconnect (${this.reconnectAttempts}/${this.maxReconnectAttempts})...`);
          setTimeout(() => {
            this.connect();
          }, 3000);
        }
      });

      this.connection.start()
        .then(() => {
          console.log(' SignalR connected successfully');
          this.reconnectAttempts = 0;
        })
        .catch((error) => {
          console.error(' SignalR connection failed:', error);
        });

    } catch (error) {
      console.error(' SignalR initialization failed:', error);
      throw error;
    }
  }

  disconnect() {
    if (this.connection) {
      this.isIntentionallyClosed = true;
      this.connection.stop();
      this.connection = null;
      console.log('SignalR disconnected');
    }
  }

  onMessage(callback: (message: any) => void) {
    this.messageCallbacks.push(callback);

    return () => {
      this.messageCallbacks = this.messageCallbacks.filter(cb => cb !== callback);
    };
  }

  async sendMessage() {
    try {
      const config = getConfig();
      const apiUrl = `${config.domain}/api/Message`;

      console.log('=== Send Message API ===');
      console.log('URL:', apiUrl);

      const response = await fetch(apiUrl, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({}),
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      console.log('Send Message Response:', data);
      return data;
    } catch (error) {
      console.error('Send Message failed:', error);
      throw error;
    }
  }

  isConnected(): boolean {
    return this.connection !== null && this.connection.state === signalR.HubConnectionState.Connected;
  }

  getReadyState(): signalR.HubConnectionState | null {
    return this.connection?.state ?? null;
  }
}

export const websocketService = new WebSocketService();
