import React, { useRef, useEffect, useState } from 'react';
import { X, Camera } from 'lucide-react';
import { scanBarcode } from '../../services/inspection';

interface Props {
  isOpen: boolean;
  onClose: () => void;
  onBarcodeDetected: (barcode: string) => void;
}

export const BarcodeScannerModal: React.FC<Props> = ({
  isOpen,
  onClose,
  onBarcodeDetected,
}) => {
  const videoRef = useRef<HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [error, setError] = useState<string | null>(null);
  const [isScanning, setIsScanning] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  useEffect(() => {
    let stream: MediaStream | null = null;
    let timer: NodeJS.Timeout | null = null;

    const stopCamera = () => {
      if (timer) clearInterval(timer);
      if (stream) stream.getTracks().forEach(t => t.stop());
    };

    const captureAndScan = async () => {
      if (!videoRef.current || !canvasRef.current) return;
      const video = videoRef.current;
      const canvas = canvasRef.current;
      const ctx = canvas.getContext('2d');
      if (!ctx || video.readyState < 2) return;

      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      ctx.drawImage(video, 0, 0, canvas.width, canvas.height);
      const blob = await new Promise<Blob | null>(res => canvas.toBlob(res, 'image/jpeg', 0.8));
      if (!blob) return;

      try {
        const fd = new FormData();
        fd.append('file', blob);
        const res = await scanBarcode(fd);
        const code = res?.results?.[0]?.code?.trim();
        if (code) {
          stopCamera();
          onBarcodeDetected(code);
          onClose();
        }
      } catch (e) {
        console.error(':', e);
      }
    };

    const start = async () => {
      try {
        stream = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: 'environment' },
        });
        if (videoRef.current) videoRef.current.srcObject = stream;
        setIsScanning(true);
        timer = setInterval(captureAndScan, 600);
      } catch (err: any) {
        setError('');
      }
    };

    if (isOpen) start();
    else stopCamera();
    return () => stopCamera();
  }, [isOpen, onBarcodeDetected, onClose]);

  if (!isOpen) return null;

  /**  **/
  if (!isMobile) {
    return (
      <div className="fixed inset-0 bg-black bg-opacity-80 flex items-center justify-center z-50">
        <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full">
          <div className="flex items-center justify-between p-4 border-b">
            <div className="flex items-center">
              <Camera className="w-6 h-6 text-blue-600 mr-2" />
              <h3 className="text-lg font-semibold text-gray-900"></h3>
            </div>
            <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
              <X className="w-6 h-6" />
            </button>
          </div>

          <div className="p-4">
            {error && (
              <div className="mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm">
                {error}
              </div>
            )}
            <div className="relative bg-black rounded-lg overflow-hidden" style={{ aspectRatio: '16/9' }}>
              <video ref={videoRef} autoPlay playsInline muted className="w-full h-full object-cover" />
            </div>

            <canvas ref={canvasRef} className="hidden" />

            <div className="mt-4 text-center text-sm text-gray-600">
              <p></p>
              <p className="text-xs text-gray-500 mt-1">QR Code</p>
            </div>

            <div className="mt-4 flex justify-end">
              <button
                onClick={onClose}
                className="px-4 py-2 border border-gray-300 text-gray-700 rounded-md hover:bg-gray-50"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }

  /**  **/
  return (
    <div className="fixed inset-0 bg-black text-white flex flex-col z-50">
      <video ref={videoRef} autoPlay playsInline muted className="absolute inset-0 w-full h-full object-cover" />

      {/*  */}
      <div className="relative z-10 p-4 flex justify-between items-center bg-black bg-opacity-40 backdrop-blur-sm">
        <div className="flex items-center">
          <Camera className="w-5 h-5 text-blue-400 mr-2" />
          <h2 className="text-lg font-semibold"></h2>
        </div>
        <button onClick={onClose}>
          <X className="w-6 h-6 text-gray-300" />
        </button>
      </div>

      {/*  */}
      <div className="flex-1 flex items-center justify-center">
        {error && (
          <div className="bg-red-600 bg-opacity-60 text-sm px-4 py-2 rounded-lg">{error}</div>
        )}
      </div>

      {/*  */}
      <div className="relative z-10 bg-black bg-opacity-60 text-center py-3 pb-[env(safe-area-inset-bottom)]">
        {!error && (
          <>
            <p className="text-sm"></p>
            <p className="text-xs text-gray-400">QR Code</p>
          </>
        )}
        <button
          onClick={onClose}
          className="mt-3 px-4 py-2 border border-gray-400 rounded-lg text-gray-300 hover:bg-gray-700"
        >
          
        </button>
      </div>

      <canvas ref={canvasRef} className="hidden" />
    </div>
  );
};
