import React from 'react';
import { X, Eye, Package, Calendar, FileText, CheckCircle, Send } from 'lucide-react';
import { TextVisionResult } from '../../types/inspection';
import { apiCall } from '../../utils/api';
import { addSubContent } from '../../services/inspection';
import { getLoggedName } from '../../services/auth';
import LoadingSpinner from '../common/LoadingSpinner';

interface TextVisionResultModalProps {
  isOpen: boolean;
  onClose: () => void;
  result: TextVisionResult | null;
  onConfirmSubmit?: () => void;
}

export const TextVisionResultModal: React.FC<TextVisionResultModalProps> = ({
  isOpen,
  onClose,
  result,
  onConfirmSubmit,
}) => {
  const [loading, setLoading] = React.useState(false);
  const [error, setError] = React.useState<string | null>(null);

  if (!isOpen || !result) return null;

  // 
  const formatExpiryDate = (dateStr: string): string => {
    if (!dateStr) return '';
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('zh-TW', { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit' 
      }).replace(/\//g, '/');
    } catch {
      return dateStr;
    }
  };

  // 
  const formatConfidence = (conf: string): string => {
    const confidence = parseFloat(conf);
    return `${(confidence * 100).toFixed(1)}%`;
  };

  // 
  const getConfidenceColor = (conf: string): string => {
    const confidence = parseFloat(conf);
    if (confidence >= 0.9) return 'text-green-600';
    if (confidence >= 0.7) return 'text-yellow-600';
    return 'text-red-600';
  };

  // 
  const handleConfirmSubmit = async () => {
    if (!result) return;

    const loggedName = getLoggedName();
    if (!loggedName) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      //  check API
      console.log('=== Check API Request Debug ===');
      const checkRequest = {
        ValueAry: [result.batch_id, result.GUID]
      };
      console.log('Check Request Data:', JSON.stringify(checkRequest, null, 2));
      
      const checkResponse = await apiCall('/api/pcmpo/check', {
        method: 'POST',
        body: checkRequest,
      });
      
      console.log('Check Response:', JSON.stringify(checkResponse, null, 2));
      
      if (checkResponse.Code !== 200) {
        setError(checkResponse.Result || '');
        return;
      }

      // 
      console.log('=== Add Sub Content API Request Debug ===');
      
      //  API 
      const formattedExpiryDate = result.expirydate ? `${result.expirydate} 00:00:00` : '';
      
      const addSubContentResponse = await addSubContent(
        result.Master_GUID,
        result.batch_num,
        result.qty,
        formattedExpiryDate,
        loggedName
      );
      
      console.log('Add Sub Content Response:', JSON.stringify(addSubContentResponse, null, 2));
      
      if (addSubContentResponse.Code === 200) {
        console.log(' ');
        if (onConfirmSubmit) {
          onConfirmSubmit();
        }
        onClose();
      } else {
        setError(addSubContentResponse.Result || '');
      }
    } catch (err) {
      console.error('Confirm submit error:', err);
      console.log(' ');
      setError(err instanceof Error ? err.message : '');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-hidden">
        <div className="flex items-center justify-between p-6 border-b bg-gray-50">
          <div>
            <h3 className="text-lg font-semibold text-gray-900 flex items-center">
              
            </h3>
            <div className="flex items-center mt-1">
              <CheckCircle className="w-4 h-4 text-green-500 mr-1" />
              <span className="text-sm text-gray-600">{result.Result}</span>
            </div>
          </div>
          <div className="flex items-center gap-3">
            <button
              onClick={handleConfirmSubmit}
              disabled={loading}
              className="px-4 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center"
            >
              {loading ? (
                <>
                  <LoadingSpinner size="small" className="mr-2" />
                  ...
                </>
              ) : (
                <>
                  <Send className="w-4 h-4 mr-2" />
                  
                </>
              )}
            </button>
            <button
              onClick={onClose}
              className="text-gray-400 hover:text-gray-600 transition-colors"
            >
              <X className="w-6 h-6" />
            </button>
          </div>
        </div>

        <div className="p-6 overflow-y-auto max-h-[calc(90vh-120px)]">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/*  */}
            <div>
              <h4 className="text-md font-medium text-gray-900 mb-3"></h4>
              {result.base64 ? (
                <div className="border rounded-lg overflow-hidden">
                  <img
                    src={result.base64.startsWith('data:') ? result.base64 : `data:image/jpeg;base64,${result.base64}`}
                    alt=""
                    className="w-full h-auto max-h-96 object-contain"
                  />
                </div>
              ) : (
                <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
                  <FileText className="w-12 h-12 text-gray-400 mx-auto mb-2" />
                  <p className="text-gray-500"></p>
                </div>
              )}
            </div>

            {/*  */}
            <div className="space-y-6">
              <div>
                <h4 className="text-md font-medium text-gray-900 mb-3"></h4>
                
                {/*  */}
                <div className="bg-blue-50 rounded-lg p-4 mb-4">
                  <h5 className="text-sm font-medium text-blue-800 mb-2 flex items-center">
                    <Package className="w-4 h-4 mr-1" />
                    
                  </h5>
                  <div className="space-y-2">
                    <div>
                      <span className="text-sm text-blue-600">:</span>
                      <span className="text-sm font-medium text-blue-900 ml-2">{result.name}</span>
                    </div>
                    <div>
                      <span className="text-sm text-blue-600">:</span>
                      <span className="text-sm font-medium text-blue-900 ml-2">{result.cht_name}</span>
                    </div>
                    <div>
                      <span className="text-sm text-blue-600">:</span>
                      <span className="text-sm font-medium text-blue-900 ml-2">{result.code}</span>
                    </div>
                  </div>
                </div>

                {/*  -  */}
                <div className="bg-green-50 rounded-lg p-4 mb-4 border-2 border-green-200">
                  <h5 className="text-sm font-medium text-green-800 mb-2"></h5>
                  <div className="text-center">
                    <div className="text-3xl font-bold text-green-700 mb-1">
                      {result.qty}
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="bg-yellow-50 rounded-lg p-4 mb-4">
                  <h5 className="text-sm font-medium text-yellow-800 mb-2"></h5>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-yellow-900">
                      {result.batch_num}
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="bg-purple-50 rounded-lg p-4 mb-4">
                  <h5 className="text-sm font-medium text-purple-800 mb-2"></h5>
                  <div className="text-center">
                    <div className="text-2xl font-bold text-purple-900">
                      {formatExpiryDate(result.expirydate)}
                    </div>
                  </div>
                </div>

                {/*  */}
                <div className="bg-gray-50 rounded-lg p-4">
                  <h5 className="text-sm font-medium text-gray-800 mb-2"></h5>
                  <div className="space-y-2 text-sm">
                    <div className="flex justify-between">
                      <span className="text-gray-600">:</span>
                      <span className="font-medium text-gray-900">{result.po_num}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">:</span>
                      <span className="font-medium text-gray-900">{result.op_name}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">:</span>
                      <span className="font-medium text-gray-900">{result.op_time}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="text-gray-600">:</span>
                      <span className={`font-medium ${result.check === '' ? 'text-yellow-600' : 'text-green-600'}`}>
                        {result.check}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {error && (
          <div className="mx-6 mb-4 p-3 bg-red-50 border border-red-200 text-red-700 rounded-lg text-sm flex items-center">
            <CheckCircle className="w-4 h-4 mr-2 flex-shrink-0" />
            {error}
          </div>
        )}

        <div className="flex justify-end p-6 border-t bg-gray-50">
          <button
            onClick={onClose}
            className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors"
          >
            
          </button>
        </div>
      </div>
    </div>
  );
};